<script>
  import maintop from "../../../components/maintop";

  export default {
    name: "user-main",
    components: {
      'main-top': maintop
    },
    data() {
      return {
        userDetail: {},
        userJobNum: {},
        searchText: '',
        cityList: [],
        jobTypeList: [],
        industryList: [],
        regionList: [],
        workYearList: [],
        backgroundList: [],
        salaryList: [],
        financingList: [],
        sizeList: [],
        searchConditions: {
          jobTitle: '',
          expectJobs: '',
          industry: '',
          workCity: '',
          region: '',
          workYear: '',
          limitEducationBackground: '',
          salaryLimit: '',
          financingStage: '',
          companySize: '',
          current: 1,
          size: 10,
          type: 1
        },
        jobShowData: {},
        nowStateList: ['离校-随时到岗', '在校-暂不考虑', '在校-考虑机会', '在校-月内到港'],
        userFile: {},
        requestHead: {
          'jwt_access_token': ''
        },
        uploadResumeData: {
          'type': '1'
        },
        jobRecommendListType: 1,
        regionIndex: -1,
        isLoading: false,
        isUpload: false
      }
    },
    methods: {
      initData() {
        //获取用户详细信息
        this.userDetail = JSON.parse(localStorage.getItem('userDetail'));
        //初始化查询条件
        this.searchConditions.workCity = this.userDetail.workCity
        this.searchConditions.expectJobs = this.userDetail.expectJobs
        //设置请求头
        this.requestHead.jwt_access_token = localStorage.getItem(this.APPConstant.JWT_ACCESS_TOKEN)
        //城市下拉值
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataCascade?fatherId=30012')
          .then(res => {
            this.cityList = res.data.data
          })
        //职位类型下拉值
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataCascade?fatherId=40001')
          .then(res => {
            this.jobTypeList = res.data.data
          })
        //行业下拉值
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataSimple?fatherId=40003')
          .then(res => {
            this.industryList = res.data.data
          })
        //所选市的对应区列表
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataSimple?dmName=' + this.searchConditions.workCity)
          .then(res => {
            this.regionList = res.data.data
          })
        //工作经验下拉
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataSimple?fatherId=40004')
          .then(res => {
            this.workYearList = res.data.data
          })
        //学历要求下拉
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataSimple?fatherId=40005')
          .then(res => {
            this.backgroundList = res.data.data
          })
        //薪资范围下拉
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataSimple?fatherId=40006')
          .then(res => {
            this.salaryList = res.data.data
          })
        //融资阶段下拉
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataSimple?fatherId=40007')
          .then(res => {
            this.financingList = res.data.data
          })
        //公司规模下拉
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataSimple?fatherId=40008')
          .then(res => {
            this.sizeList = res.data.data
          })
        //获取用户各种操作职位数量
        this.$axios.get(this.APPConstant.API_HEAD + '/re-apply-job-record/getAllCollectionNum')
          .then(res => {
            this.userJobNum = res.data.data
          })
        //获取用户上传文件
        this.getAllUserFile();

        //初始化职位列表
        this.searchJobList(1);

      },
      replaceRegion(index, region) {
        this.regionIndex = index;
        this.searchConditions.region = region;
        this.searchJobList(1)
      },
      clearConditions() {
        var searchConditions = this.searchConditions;
        for (var val in searchConditions) {
          if (val != 'current' && val != 'size' && val != 'type') {
            searchConditions[val] = '';
          }
        }
        this.searchConditions.workCity = this.userDetail.workCity
        this.searchConditions.expectJobs = this.userDetail.expectJobs
        this.regionIndex = -1;
        this.searchJobList(1);
      },
      jobRecommendSelect(key, keyPath) {
        this.jobRecommendListType = key;
        this.searchConditions.type = key;
        this.searchJobList(1);
      },
      toJobDetail(jobId) {
        let routeUrl = this.$router.resolve('/jobDetail?jobId=' + jobId);
        window.open(routeUrl.href, '_blank');
      },
      toCompanyDetail(companyId) {
        let routeUrl = this.$router.resolve('/companyDetail?companyId=' + companyId)
        window.open(routeUrl.href, '_blank');
      },
      uploading(){
        this.isUpload = true;
      },
      uploadSuccess(){
        this.isUpload = false;
        this.$refs['resume-upload'].clearFiles();
        this.getAllUserFile()
      },
      getAllUserFile() {
        this.$axios.get(this.APPConstant.API_HEAD + '/re-user-file/getAllUserFile')
          .then(res => {
            this.userFile = res.data.data
          })
      },
      deleteResume(resumeItem) {
        let _this = this;
        this.$axios.post(this.APPConstant.API_HEAD + '/re-user-file/deleteFile', resumeItem)
          .then(res => {
            if (res.data.code == 0) {
              _this.getAllUserFile()
            }
          })
      },
      //查询职位列表
      searchJobList(param) {
        this.searchConditions.current = param;
        this.isLoading = true;
        if (this.searchConditions.workCity instanceof Array) {
          this.searchConditions.workCity = this.searchConditions.workCity[1];
        }
        if (this.searchConditions.expectJobs instanceof Array) {
          this.searchConditions.expectJobs = this.searchConditions.expectJobs[1];
        }
        this.$axios.post(this.APPConstant.API_HEAD + '/re-job/searchJob', this.searchConditions)
          .then(res => {
            this.jobShowData = res.data.data
            this.isLoading = false
          })
        //所选市的对应区列表
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataSimple?dmName=' + this.searchConditions.workCity)
          .then(res => {
            this.regionList = res.data.data
          })
      }
    },
    created() {
      this.initData();
    }
  }
</script>

