  .selected-box .clearCondtions:hover {
    color: #00d7c6;
  }

  /*.user-aside {
    background: #fff;
    margin-top: 20px;
    padding: 20px 30px;
    margin-bottom: 16px;
  }*/

  .aside-box {
    background: #fff;
    margin-top: 20px;
    padding: 20px 30px;
    margin-bottom: 16px;
  }

  .file-main {
    margin-top: 10px;
  }

  .file-item {
    position: relative;
    color: #414a60;
    font-size: 14px;
    cursor: default;
    margin-top: 10px;
  }

  .file-item .el-icon-delete {
    float: right;
  }

  .file-btns {
    /*height: 30px;*/
    margin-top: 10px;
    text-align: center;
  }

  .head-box, .username, .userinfo, .now-state, .user-stat {
    text-align: center;
    margin-bottom: 10px;
  }

  .aside-userinfo {
    position: relative;
  }

  .aside-userinfo .edit-userinfo {
    position: absolute;
    top: 20px;
    right: 40px;
    z-index: 1;
    line-height: 1;
  }

  .edit-userinfo:hover {
    color: #00c2b3;
  }

  .head-img {
    border-radius: 200px;
  }

  .userinfo span {
    font-size: 12px;
  }

  .now-state {
    margin-top: 20px;
  }

  .now-state-inner {
    width: 80%;
    border: #8d92a1 1px solid;
    margin: 0 auto;
  }

  .stat-item {
    float: left;
    margin-left: 20px;
  }

  .stat-item a b {
    color: #414a60;
    display: block;
    font-weight: 400;
    font-size: 15px
  }

  .stat-item p {
    font-size: 13px;
  }

  .stat-item b:hover {
    color: #00c2b3;
  }


  .item-inner {
    background: #fff;
    margin-bottom: 5px;
    padding: 25px 0 20px;
  }

  .job-mess, .company-mess {
    cursor: pointer
  }

  .job-item h3 {
    /*color: #00c2b3;*/
    max-width: 300px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .job-mess:hover {
    color: #00d7c6;
  }

  .company-mess:hover {
    color: #00d7c6;
  }


  .job-item .append {
    width: 60%;
    margin-top: 5px;
    font-size: 12px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    -o-text-overflow: ellipsis;
  }

  .job-item img {
    position: absolute;
    top: 16px;
    right: 20px;
    width: 54px;
    height: 54px;
    border: 1px solid #f3f5fb;
    border-radius: 9px;
  }

  .job-item .info-append {
    padding: 5px 0 10px;
  }

  .job-item .info-append span {
    font-size: 12px;
    height: 21px;
    line-height: 21px;
    padding: 0 8px;
    border: 1px solid #eee;
    border-radius: 2px;
    display: inline-block;
    margin-right: 10px;
  }

  .job-item .info-append .info-desc {
    font-size: 13px;
  }

  .page-box {
    background: #fff;
    padding: 10px;
  }

  .resume-main {
    margin-top: 10px;
  }

  .resume-item {
    position: relative;
    color: #414a60;
    font-size: 14px;
    cursor: default;
    margin-top: 5px;
  }

  .resume-item .el-icon-delete {
    float: right;
  }

  .resume-btns {
    /*height: 30px;*/
    margin-top: 10px;
  }

  .online-resume {
    margin-top: 20px;
  }

  .online-resume p {
    margin-top: 10px;
  }

  .data-tips {
    text-align: center;
    min-height: 340px;
    position: relative;
    padding: 100px 0;
    background: #fff;
  }

  .data-tips .data-blank {

    width: 360px;
    margin: 0 auto;
    text-align: center;
  }

  .tip-errordata {
    display: block;
    width: 412px;
    height: 212px;
    margin: -20px auto 15px;
    background: url('https://static.zhipin.com/zhipin-geek/v383/web/geek/images/tip-errordata.png') 90px 0 no-repeat;
  }


</style>



