<template>
  <el-container>
    <el-header>
      <company-top></company-top>
    </el-header>
    <el-container>
      <el-aside width="200px" style="background: whitesmoke;" class="aside">
        <company-aside active="6"></company-aside>
      </el-aside>
      <el-main >
        <div class="text">
          <p >
            昨日共有<span>1500</span>人浏览过您公司的职位
          </p>
          <p>
            其中共有<span>600</span>人投递了简历
          </p>
        </div>

      </el-main>
    </el-container>

  </el-container>
</template>

<script>
  import companyTop from "../../components/companyTop";
  import companyAside from "../../components/companyAside";


  export default {
    name: "hirePage",
    components: {
      "company-top": companyTop,
      "company-aside": companyAside

    },
    data(){
      return{
        active: '5'
      }
    },
    methods:{
      changePage(key, keyPath) {
        if(key==='1'){
          this.$router.push('/boss/bossMainPage')
        }else if(key === '2'){
          this.$router.push('/boss/editCompanyPage')
        }else if (key === '3'){
          this.$router.push('/boss/editJobPage')
        }else if (key === '4'){
          this.$router.push('/boss/applyPage')
        }else {
          this.$router.push('/boss/hirePage')
        }
      }
    }
  }
</script>

<style scoped>
  .el-container {
    width: 100%;
    height: 700px;
  }

  .el-header {
    padding: 0;
  }

  .el-aside {
    height: 100%;
  }

  .text{
    text-align: center;
    margin-top: 200px;
    font-size: 50px;
    font-weight: 500;
  }
  .text p{
    margin-top: 20px;
  }

  .text p span{
    color: #fc6c38;
  }
</style>



