<template>
  <el-container>
    <el-header>
      <company-top></company-top>
    </el-header>
    <el-container>
      <el-aside width="200px" style="background: whitesmoke;" class="aside">

        <company-aside active="4"></company-aside>
      </el-aside>
      <el-main>
        <div class="main-box">
          <el-form :inline="true" class="from-box" ref="searchForm">
            <el-form-item label="职位标题">
              <el-input v-model="searchConditions.jobTitle" placeholder="职位标题"></el-input>
            </el-form-item>
            <el-form-item label="职位类别">
              <el-cascader placeholder="选择职位类别"
                           v-model="searchConditions.jobCategory"
                           :options="jobTypeList"
                           :props="{ expandTrigger: 'hover' }"
                           :show-all-levels="false"
                           clearable>
              </el-cascader>
            </el-form-item>

            <el-form-item label="招聘状态">
              <el-select v-model="searchConditions.status" placeholder="选择">
                <el-option label="招聘中" value="1"></el-option>
                <el-option label="停止招聘" value="0"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" icon="el-icon-search" size="mini" @click="searchJobs">
                搜索
              </el-button>
              <el-button icon="el-icon-refresh-right" size="mini" @click="resetForm()">
                重置
              </el-button>
            </el-form-item>
          </el-form>
          <div class="btn-box">
            <el-button type="success" @click="editJob(null,null)" icon="el-icon-plus" size="mini">新增</el-button>
            <el-dialog class="dialog" title="新增招聘职位" :visible.sync="editFormVisible">
              <el-form :model="jobTemp">
                <el-form-item label="职位标题" label-width="120px">
                  <el-input placeholder="职位标题" v-model="jobTemp.jobTitle"></el-input>
                </el-form-item>
                <el-form-item v-if="cityList!=null" label="所在市" label-width="120px">
                  <el-cascader placeholder="选择市"
                               v-model="jobTemp.city"
                               :options="cityList"
                               :props="{ expandTrigger: 'hover' }"
                               :show-all-levels="false"
                               @change="changeCity"
                               clearable>
                  </el-cascader>
                </el-form-item>
                <el-form-item v-if="regionList!=null" label="所在区" label-width="120px">
                  <el-select placeholder="选择区" v-model="jobTemp.region">
                    <el-option v-for="(regionItem,index) in regionList"
                               :key="index"
                               :label="regionItem.dmName"
                               :value="regionItem.dmName"
                               >

                    </el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="详细地址" label-width="120px">
                  <el-input placeholder="填写地址" v-model="jobTemp.address"></el-input>
                </el-form-item>
                <el-form-item label="职位行业" label-width="120px">
                  <el-select v-model="jobTemp.jobIndustry" placeholder="选择">
                    <el-option v-for="(industry,index) in industryList" :key="index" :label="industry.dmName" :value="industry.dmName"></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item v-if="jobTypeList!=null" label="职位类别" label-width="120px">
                  <el-cascader placeholder="选择职位类别"
                               @change="changeJobCategory"
                               v-model="jobTemp.jobCategory"
                               :options="jobTypeList"
                               :props="{ expandTrigger: 'hover' }"
                               :show-all-levels="false"
                               clearable>
                  </el-cascader>
                </el-form-item>
                <el-form-item label="职位描述" label-width="120px">
                  <mavon-editor
                    v-model="jobTemp.jobDescriptionMarkDown"
                    defaultOpen='edit'
                    @change="changeJobDescription"
                    :toolbars="this.APPConstant.toolbars"
                    @keydown=""
                  />
                </el-form-item>
                <el-form-item v-if="salaryRangeList!=null" label="薪资范围" label-width="120px">
                  <el-select placeholder="选择范围" v-model="jobTemp.salaryRange">
                    <el-option v-for="(salaryRangeItem,index) in salaryRangeList"
                               :key="index"
                               :label="salaryRangeItem.dmName"
                               :value="salaryRangeItem.dmName"
                    ></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item v-if="salaryList!=null" label="最低薪资" label-width="120px">
                  <el-select placeholder="选择范围" v-model="jobTemp.minSalary">
                    <el-option v-for="(salaryItem,index) in salaryList"
                               :key="index"
                               :label="salaryItem.dmName"
                               :value="salaryItem.dmName"
                    ></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item v-if="salaryList!=null" label="最高薪资" label-width="120px">
                  <el-select placeholder="选择范围" v-model="jobTemp.maxSalary">
                    <el-option v-for="(salaryItem,index) in salaryList"
                               :key="index"
                               :label="salaryItem.dmName"
                               :value="salaryItem.dmName"
                    ></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="发放期数" label-width="120px">
                  <el-input type="number" v-model="jobTemp.salaryNums" placeholder="填写期数"></el-input>
                </el-form-item>
                <el-form-item v-if="workYearList!=null" label="工作经验" label-width="120px">
                  <el-select placeholder="不限" v-model="jobTemp.workYear">
                    <el-option v-for="(workYearItem,index) in workYearList" :key="index" :label="workYearItem.dmName"
                               :value="workYearItem.dmName"></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item v-if="limitEducationBackgroundList!=null" label="学历限制" label-width="120px">
                  <el-select placeholder="不限" v-model="jobTemp.limitEducationBackground">
                    <el-option v-for="(education,index) in limitEducationBackgroundList" :key="index" :label="education.dmName"
                               :value="education.dmName"></el-option>
                  </el-select>
                </el-form-item>
              </el-form>
              <div slot="footer" class="dialog-footer">
                <el-button @click="editFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="commitJobTemp">确 定</el-button>
              </div>
            </el-dialog>
          </div>
          <el-table
            class="table-box"
            :data="jobData"
            ref="multipleTable"
            tooltip-effect="dark"
            :default-sort="{prop: 'date', order: 'descending'}"
            height="500"
            max-height="500"
          >

            <el-table-column
              label="职位标题"
              prop="jobTitle"
              width="120"
            >
            </el-table-column>
            <el-table-column
              prop="jobCategory"
              label="职位类别"
            >
            </el-table-column>
            <el-table-column
              prop="salaryRange"
              label="薪资范围"
              show-overflow-tooltip>
            </el-table-column>
            <el-table-column
              prop="minSalary"
              label="最低薪水"
              show-overflow-tooltip>
            </el-table-column>
            <el-table-column
              prop="maxSalary"
              label="最高薪水"
              show-overflow-tooltip>
            </el-table-column>
            <el-table-column
              prop="salaryNums"
              label="发放期数"
              show-overflow-tooltip>
            </el-table-column>
            <el-table-column
              prop="workYear"
              label="经验限制"
              show-overflow-tooltip>
            </el-table-column>
            <el-table-column
              prop="limitEducationBackground"
              label="最低学历"
              show-overflow-tooltip>
            </el-table-column>
            <el-table-column
              prop="statusApplication"
              label="状态"
              show-overflow-tooltip>
            </el-table-column>
            <el-table-column
              width="120px"
              prop="createTime"
              label="创建时间"
              show-overflow-tooltip>
            </el-table-column>
            <el-table-column

              label="操作"
            >
              <template slot-scope="scope">
                <el-button
                  @click="changeApply(scope.$index,scope.row)"
                  type="text"
                  size="small">
                  {{scope.row.status == '1'?'停止招聘':'开启招聘'}}
                </el-button>
                <el-button
                  type="text"
                  size="small"
                  @click="editJob(scope.index,scope.row)">
                  编辑
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>

      </el-main>
    </el-container>

  </el-container>
</template>

<script>
  import companyTop from "../../components/companyTop";
  import companyAside from "../../components/companyAside";

  export default {
    name: "editJobPage",
    components: {
      "company-top": companyTop,
      "company-aside": companyAside
    },
    data() {
      return {
        test: 1,
        active: '3',
        jobData: [],
        jobTemp: {
          jobCategory:"",
          maxSalary:"",
          bossId:"",
          city:"",
          jobTitle:"",
          salaryNums:"",
          minSalary:"",
          jobIndustry:"",
          jobId:"",
          companyId:"",
          province:"",
          createTime:"",
          limitEducationBackground:"",
          statusApplication:"",
          jobDescription:"",
          workYear:"",
          region:"",
          status:"",
          salaryRange:""
        },
        searchConditions: {
          jobTitle: '',
          jobCategory: '',
          status: ''
        },
        jobTypeList:[],
        regionList: [],
        cityList: null,
        industryList: null,
        financingList: null,
        sizeList: null,
        workYearList: null,
        salaryList: null,
        salaryRangeList: null,
        limitEducationBackgroundList: null,
        editFormVisible: false
      }
    },
    methods: {
      commitJobTemp(){
        this.$axios.post(this.APPConstant.API_HEAD+'/re-company-boss/updateJob',this.jobTemp)
          .then(res => {
            if(res.data.code === 0){
              this.$alert('操作成功！' , '提示', {
                confirmButtonText: '确定',
                callback: action => {
                  this.getJobData()
                  this.editFormVisible = false
                }
              });
            }
          })
      },
      searchJobs(){
        this.$axios.post(this.APPConstant.API_HEAD+'/re-company-boss/searchCompanyJob',this.searchConditions)
        .then(res => {
          if(res.data.code === 0){
            this.jobData = res.data.data;
          }
        })
      },
      resetForm(formName) {
        this.searchConditions.jobCategory = null;
        this.searchConditions.status = null;
        this.searchConditions.jobTitle = null;
        this.searchJobs()
      },
      changeJobDescription(value, render) {
        this.jobTemp.jobDescription = render;
      },
      changeApply(index, row) {
        this.$axios.get(this.APPConstant.API_HEAD +
          '/re-company-boss/changeApply?jobId=' + row.jobId
          + '&status=' + row.status)
          .then(res => {
            if (res.data.code === 0) {
              this.$alert('操作成功！' + row.jobTitle, '提示', {
                confirmButtonText: '确定',
                callback: action => {
                  this.getJobData()
                }
              });
            }
          })
      },
      editJob(index, row) {
        if (row != null) {
          this.getRegionData(row.city)
          //转换markdown
          const TurndownService = require('turndown').default
          var turndownService = new TurndownService();
          this.jobTemp = JSON.parse(JSON.stringify(row));
          if (this.jobTemp.jobDescription != null) {
            this.jobTemp.jobDescriptionMarkDown
              = turndownService.turndown(this.jobTemp.jobDescription);
          } else {
            this.jobTemp.jobDescriptionMarkDown = '';
          }
        } else {
          for(var key in this.jobTemp){
            this.jobTemp[key] = '';
          }
        }

        this.editFormVisible = true;
      },
      changeCity(city){
        this.jobTemp.region = '';
        this.jobTemp.province = city[0];
        this.jobTemp.city = city[1];
        this.getRegionData(city[1])
      },
      changeJobCategory(jobCategory){
        this.jobTemp.jobCategory = jobCategory[1];
      },
      getRegionData(city) {
        if (city instanceof Array) {
          city = city[1];
        }
        console.log(city)
        //获取区数据
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataSimple?dmName=' + city)
          .then(res => {
            this.regionList = res.data.data
          })
      },
      getDropData() {
        //城市下拉值
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataCascade?fatherId=30012')
          .then(res => {
            this.cityList = res.data.data
          })
        //职位类型下拉值
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataCascade?fatherId=40001')
          .then(res => {
            this.jobTypeList = res.data.data
          })
        //行业下拉值
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataSimple?fatherId=40003')
          .then(res => {
            this.industryList = res.data.data
          })
        //薪资下拉值
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataSimple?fatherId=40002')
          .then(res => {
            this.salaryList = res.data.data
          })
        //薪资范围下拉值
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataSimple?fatherId=40006')
          .then(res => {
            this.salaryRangeList = res.data.data
          })
        //工作经验下拉值
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataSimple?fatherId=40004')
          .then(res => {
            this.workYearList = res.data.data
          })
        //学历限制下拉值
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataSimple?fatherId=40005')
          .then(res => {
            this.limitEducationBackgroundList = res.data.data
          })
      },
      getJobData() {
        this.$axios.get(this.APPConstant.API_HEAD + '/re-company-boss/getCompanyJobs')
          .then(res => {
            if (res.data.code === 0) {
              this.jobData = res.data.data
            }
          })
      },
      initData() {
        this.getJobData();
        this.getDropData();
      }
    }
    ,
    created() {
      this.initData();
    }
  }
</script>

<style scoped>


  .el-form--inline .el-form-item {

    margin-right: 33px;

  }

  .el-container {
    width: 100%;
    height: 700px;
  }

  .el-header {
    padding: 0;
  }

  .el-aside {
    height: 100%;
  }


  .main-box {
    width: 90%;
    margin-left: 50px;
  }

  .from-box {
    display: flex;
    align-items: center;
  }

  .dialog .el-input, .dialog .el-select {
    width: 300px;
  }
</style>



