<template>
  <div id="userCollection">
    <main-top selected="/user/center"></main-top>
    <el-row id="controller" :gutter="30">
      <el-col :span="13" :offset="3" class="main">
        <el-menu :default-active="tag" class="job-top" mode="horizontal" @select="changeTag">
          <el-menu-item index="0">浏览过</el-menu-item>
          <el-menu-item index="1">已投递</el-menu-item>
          <el-menu-item index="2">面试</el-menu-item>
          <el-menu-item index="3">感兴趣</el-menu-item>
        </el-menu>
        <div class="collection-job-main">
          <div class="collection-job-item" v-for="(jobItem,index) in jobData.list" :key="index">
            <div class="boss-img">
              <img src="../../../assets/img4.jpg"/>
            </div>
            <div class="item-content">
              <div class="info-header">
                <p>李女士&#12288;|&#12288;研发主管</p>
                <el-button class="cancel-collection"
                           size="mini"
                           v-if="tag==3"
                           @click="cancelCollectionJob(jobItem.jobId)"
                >
                  取消感兴趣
                </el-button>

                <el-button class="cancel-collection"
                           size="mini"
                           :disabled="jobItem.deliveryStatus == '2'"
                           v-if="tag==2"
                           @click="getFeedbackData(jobItem)"
                >
                  {{jobItem.btnText}}
                </el-button>
              </div>
              <div
                class="info-main">
                <div class="job-info">
                  <a :href="'/jobDetail?jobId='+jobItem.jobId" target="_blank">
                    <span>{{jobItem.jobTitle}}</span>
                    <span class="salary">{{jobItem.minSalary}}-{{jobItem.maxSalary}}·{{jobItem.salaryNums}}薪</span>
                  </a>
                  <p>
                    {{jobItem.jobCity}}&#12288;{{jobItem.jobRegion}}&#12288;开发区东区
                    <el-divider direction="vertical"></el-divider>
                    {{jobItem.workYear==null?'经验不限':jobItem.workYear}}
                    <el-divider direction="vertical"></el-divider>
                    {{jobItem.limitEducationBackground==null?'学历不限':jobItem.limitEducationBackground}}
                  </p>
                </div>
                <div class="company-info">
                  <div class="company-logo">
                    <img :src="jobItem.imgUrl"/>
                  </div>
                  <a :href="'/companyDetail?companyId='+jobItem.companyId" target="_blank">
                    {{jobItem.companyName}}
                  </a>
                  <p>
                    {{jobItem.industry}}
                    .
                    {{jobItem.financingStage}}
                    .
                    {{jobItem.companySize}}
                  </p>
                </div>
                <br clear="both">
              </div>
            </div>
          </div>
          <el-row class="page-box">
            <el-col :span="12" :offset="6" class="page-box-inner">
              <el-pagination v-if="tag != 3"
                :page-size="jobData.pageSize"
                :current-page="jobData.pageNum"
                layout="prev, pager, next"
                @current-change="getJobData"
                :total="jobData.total">
              </el-pagination>
              <el-pagination v-else
                             :page-size="jobData.pageSize"
                             :current-page="jobData.pageNum"
                             layout="prev, pager, next"
                             @current-change="getCollectioinJob"
                             :total="jobData.total">
              </el-pagination>
            </el-col>
          </el-row>
          <el-dialog class="dialog-box" :title="'职位'+ feedbackTemp.jobTitle+'的应聘结果'"
                     :visible.sync="feedbackVisitalbe">
            <div class="dialog-main">
              <p>面试岗位：<span>{{feedbackTemp.jobTitle}}</span></p>

              <p>是否通过面试：<span>{{feedbackTemp.result==1?'是':'否'}}</span></p>

              <div class="text" v-html="feedbackTemp.description"></div>
              <img :src="feedbackTemp.result==1?successDeliveryImg:failDeliveryImg">

            </div>


            <div slot="footer" class="dialog-footer">
              <el-button @click="feedbackVisitalbe = false">返回</el-button>
            </div>
          </el-dialog>

        </div>
      </el-col>
      <el-col :span="5" class="user-aside">
        <div class="aside-userinfo aside-box">
          <a href="/user/resume" class="edit-userinfo">编辑</a>
          <div class="head-box">
            <img src="../../../assets/img4.jpg" class="head-img" height="60" width="60"/>
          </div>
          <div class="username">
            <span>{{userDetail.name}}</span>
          </div>
          <div class="userinfo">
            <span>{{userDetail.age}}岁</span>&#12288;|&#12288;
            <span>21年应届生</span>&#12288;|&#12288;
            <span>{{userDetail.highestEducationBackground}}</span>
          </div>
          <div class="now-state">
            <div class="now-state-inner">
              <el-select v-model="userDetail.jobStatus" placeholder="请选择">
                <el-option
                  v-for="(item,index) in nowStateList"
                  :key="index"
                  :label="item.dmName"
                  :value="item.dmName">
                </el-option>
              </el-select>
            </div>
          </div>
          <div class="user-stat">
            <div class="stat-item">
              <a href="/user/userCollection?tag=0" class="">
                <b>{{userJobNum.viewedNum}}</b>
                <p>浏览过</p>
              </a>
            </div>
            <div class="stat-item">
              <a href="/user/userCollection?tag=1" class="">
                <b>{{userJobNum.deliveryNum}}</b>
                <p>已投递</p>
              </a>
            </div>
            <div class="stat-item">
              <a href="/user/userCollection?tag=2" class="">
                <b>{{userJobNum.interviewNum}}</b>
                <p>面试</p>
              </a>
            </div>
            <div class="stat-item">
              <a href="/user/userCollection?tag=3" class="">
                <b>{{userJobNum.collectionNum}}</b>
                <p>感兴趣</p>
              </a>
            </div>
            <div style="clear:both;"></div><!--清除父级元素坍塌-->
          </div>
        </div>
        <div class="aside-resume aside-box">
          <p>附件管理</p>
          <div class="resume-main">
            <div v-for="(resumeItem,index) in userFile.resumeList" :key="index" class="resume-item">
              <i class="el-icon-document"></i>
              <a :href="resumeItem.fileUrl" target="_blank">
                <span>{{resumeItem.fileName}}</span>
              </a>
              <el-popconfirm
                title="确定删除吗？"
                @confirm="deleteResume(resumeItem)"
                @cancel=""
              >
                <a slot="reference">
                  <i class="el-icon-delete"></i>
                </a>
              </el-popconfirm>
            </div>
          </div>
          <div class="viveo-file" v-if="userFile.videoList != ''">
                          <div class="my-line" style="background: #DCDFE6;width: 240px;height: 1px;margin-top: 12px;margin-bottom: 12px"></div>
            <p>视频附件</p>
            <div class="file-main">
              <div v-for="(resumeItem,index) in userFile.videoList" :key="index" class="file-item">
                <i class="el-icon-document"></i>
                <a :href="resumeItem.fileUrl" target="_blank">
                  <span>{{resumeItem.fileName}}</span>
                </a>

                <el-popconfirm
                  title="确定删除吗？"
                  @confirm="deleteResume(resumeItem)"
                  @cancel=""
                >
                  <a slot="reference">
                    <i class="el-icon-delete"></i>
                  </a>
                </el-popconfirm>
              </div>
            </div>
          </div>

          <div class="resume-btns">
            <el-upload
              ref="resume-upload"
              class="upload-demo"
              :action="this.APPConstant.API_HEAD+'/re-user-file/uploadFile'"
              :data="{'type':1}"
              :headers="this.requestHead"
              :on-success="uploadSuccess"
              :show-file-list="isUpload"
              :on-progress = "uploading"
              accept=".PNG,.JPG,.DOCX,.DOC,.PDF"
            >
              <el-button onmouseenter="this.style.background = '#00d7c6' "
                         onmouseleave="this.style.background = '#00c2b3';this.style.color = '#fff' ">
                上传简历
              </el-button>
            </el-upload>

          </div>
          <div class="resume-btns">
            <el-upload
              class="upload-demo"
              :action="this.APPConstant.API_HEAD+'/re-user-file/uploadFile'"
              :data="{'type':2}"
              :headers="requestHead"
              :on-success="uploadSuccess"
              :on-progress = "uploading"
              :show-file-list="isUpload"
              accept=".wmv,.asf,.asx,.mp4,.3gp,.m4v,.mov,.rm,.rmvb,.avi,.dat,.mkv,.flv,.vob"
            >
              <el-button onmouseenter="this.style.background = '#00d7c6' "
                         onmouseleave="this.style.background = '#00c2b3';this.style.color = '#fff' ">
                上传视频
              </el-button>
            </el-upload>
          </div>


          <div class="online-resume">
                          <div class="my-line" style="background: #DCDFE6;width: 240px;height: 1px;margin-top: 12px;margin-bottom: 12px"></div>
            <div>
              <span>在线简历</span>&#12288;<a href="#">编辑</a>
            </div>
            <p>2021.02.09更新</p>
          </div>

        </div>
        <div class="aside-recommend aside-box">
          <div>
            <a href="#">对我感兴趣 </a>
          </div>
                        <div class="my-line" style="background: #DCDFE6;width: 240px;height: 1px;margin-top: 12px;margin-bottom: 12px"></div>
          <div>
            <a href="#">看过我 </a>
          </div>
        </div>
      </el-col>
    </el-row>


  </div>

</template>

<script>
  import maintop from "../../../components/maintop";

  export default {
    name: "userCollection",
    components: {
      'main-top': maintop
    },
    data() {
      return {
        userDetail: {},
        userJobNum: {},
        userFile: {},
        nowStateList: ['离校-随时到岗', '在校-暂不考虑', '在校-考虑机会', '在校-月内到港'],
        requestHead: {
          'jwt_access_token': ''
        },
        uploadResumeData: {
          'type': '1'
        },
        feedbackTemp: {
          jobTitle: '',
          result: '',
          description: ''
        },
        feedbackVisitalbe: false,
        isUpload:false,
        jobData: [],
        tag: '0',
        successDeliveryImg: 'https://qiubucket.oss-cn-guangzhou.aliyuncs.com/recruitment_web_static/img/successDelivery.png',
        failDeliveryImg: 'https://qiubucket.oss-cn-guangzhou.aliyuncs.com/recruitment_web_static/img/failDelivery.png'
      }
    },
    methods: {
      //获取面试结果
      getFeedbackData(jobItem){
        this.$axios.get(this.APPConstant.API_HEAD+'/re-apply-job-feedback/getFeedbackDatail?applyRecordId='+jobItem.applyRecordId)
          .then(res =>{
            if(res.data.code == 0){
              for (let key in this.feedbackTemp) {
                this.feedbackTemp[key] = ''
              }
              this.feedbackTemp.jobTitle = jobItem.jobTitle
              this.feedbackTemp.description = res.data.data.description;
              this.feedbackTemp.result = res.data.data.result
              this.feedbackVisitalbe = true
            }
          })
      },
      changeTag(key, keyPath) {
        this.tag = key;
        if (key == '3'){
          this.getCollectioinJob(1)
        }
        else {
          this.getJobData(1);
        }
      },
      uploading(){
        this.isUpload = true;
      },
      uploadSuccess(){
        this.isUpload = false;
        this.$refs['resume-upload'].clearFiles();
        this.getAllUserFile()
      },
      getAllUserFile() {

        this.$axios.get(this.APPConstant.API_HEAD + '/re-user-file/getAllUserFile')
          .then(res => {
            this.userFile = res.data.data
          })
      },
      deleteResume(resumeItem) {
        let _this = this;
        this.$axios.post(this.APPConstant.API_HEAD + '/re-user-file/deleteFile', resumeItem)
          .then(res => {
            if (res.data.code == 0) {
              _this.getAllUserFile()
            }
          })
      },
      getJobData(param){
        this.$axios.get(this.APPConstant.API_HEAD +
          '/re-apply-job-record/getRecordJobByType' +
          '?current='+param+'&size=10'+'&type='+this.tag)
          .then(res => {
            this.jobData = res.data.data
          })
      },
      getCollectioinJob(param){
        this.$axios.get(this.APPConstant.API_HEAD +
          '/re-user-collection-job/getCollectionJob' +
          '?current='+param+'&size=10')
          .then(res => {
            this.jobData = res.data.data
          })
      },
      cancelCollectionJob(jobId){
        this.$axios.get(this.APPConstant.API_HEAD+'/re-user-collection-job/collectionJob?jobId='+jobId)
          .then(res => {
            if (res.data.code == 0) {
              this.getCollectioinJob(this.jobData.pageNum)
            }
          })
      },
      initData() {
        //获取用户详细信息
        this.userDetail = JSON.parse(localStorage.getItem('userDetail'));
        //设置请求头
        this.requestHead.jwt_access_token = localStorage.getItem(this.APPConstant.JWT_ACCESS_TOKEN)
        this.tag = this.$route.query.tag;

        //获取用户各种操作职位数量
        this.$axios.get(this.APPConstant.API_HEAD + '/re-apply-job-record/getAllCollectionNum')
          .then(res => {
            this.userJobNum = res.data.data
          });

        //获取用户上传文件
        this.getAllUserFile();
        //获取职位列表
        if (this.tag == '3'){
          this.getCollectioinJob(1)
        }else {
          this.getJobData(1);
        }

      }
    },
    created() {
      this.initData();
    }
  }
</script>

<style scoped>
  #userCollection {
    background: #f6f6f8;
  }

  a:hover {
    color: #00c2b3;
  }

  /deep/ .el-upload {
    width: 100%;
