  }

  .company-logo img {
    width: 42px;
    height: 42px;
    border-radius: 100%;
  }

  .dialog-box .dialog-main{
    margin-left: 50px;

  }
  .dialog-box .dialog-main p{
    margin-top: 10px;
    font-size: 15px;
  }
  .dialog-main span{
    color: #fc6c38;
  }
  .dialog-main .text{
    margin-top: 20px;
    font-weight: 600;
    font-size: 15px;
  }

  .dialog-main img{
    margin-top: 50px;
    margin-left: 150px;
  }
</style>



