<template>
  <el-container>

    <el-header>
      <company-top></company-top>
    </el-header>
    <el-container>
      <el-aside width="200px" style="background: whitesmoke;" class="aside">
        <company-aside active="1"></company-aside>
      </el-aside>
      <el-main  >
        <div class="background">
          <img :src="imgSrc"  width="1200" height="600" alt=""/>
        </div>
        <div class="text">

          <p >
            今日共有<span>{{countData.viewNum}}</span>人浏览过您公司的职位
          </p>
          <p>
            其中共有<span>{{countData.deliveryNum}}</span>人投递了简历
          </p>
        </div>

      </el-main>
    </el-container>

  </el-container>
</template>

<script>
  import companyTop from "../../components/companyTop";
  import companyAside from "../../components/companyAside";

  export default {
    name: "bossMainPage",
    components: {
      "company-top": companyTop,
      "company-aside": companyAside
    },
    data(){
      return{
        countData:{

        },
        imgSrc: 'https://qiubucket.oss-cn-guangzhou.aliyuncs.com/recruitment_web_static/img/bossMainBackground2.png'
      }
    },
    methods:{
      initData(){
        this.$axios.get(this.APPConstant.API_HEAD+'/re-company-boss/getBossMainPageData')
        .then(res => {
          this.countData = res.data.data
        })
      }
    },
    created() {
      this.initData()
    }
  }
</script>

<style scoped>
  .el-container {
    width: 100%;
    height: 100%;
  }

  .el-header {
    padding: 0;
  }

  .el-aside {
    height: 100%;
  }

  .background {
    position: absolute;
    top: 130px;
    z-index: -1;
  }

  .text{
    position: relative;
    z-index: 9999;
    text-align: center;
    margin-top: 200px;
    font-size: 50px;
    font-weight: 500;
  }
  .text p{
    margin-top: 20px;
  }

  .text p span{
    color: #fc6c38;
  }
</style>



