<template>
  <div id="jobDetail">
    <main-top selected="/jobMain"></main-top>
    <el-row id="main" v-if="jobDetail !=null">
      <el-row class="job-banner">
        <el-col :span="18" :offset="3" class="inner">
          <div class="info-main">
            <div class="job-status">
              <span>最新</span>
            </div>
            <div class="job-name">
              <h1>{{jobDetail.jobTitle}}</h1>
              <span class="salary">{{jobDetail.minSalary}}~{{jobDetail.maxSalary}}</span>
            </div>
            <p>
              {{jobDetail.city}}&nbsp;.&nbsp;
              {{jobDetail.workYear==null?'经验不限':jobDetail.workYear}}&nbsp;.&nbsp;
              {{jobDetail.limitEducationBackground==null?'学历不限':jobDetail.limitEducationBackground}}</p>
            <div class="job-tags">
              <span>五险一金</span>
              <span>医疗保险</span>
              <span>定期体检</span>
              <span>加班补助</span>
              <span>年终奖</span>
              <span>加班补助</span>
              <span>加班补助</span>
              <span>...</span>
            </div>
          </div>
          <div v-if="isLogin == true" class="job-op">
            <el-button type="success"
                       :disabled = "deliveryBtnIsDisabled"
                       @click="deliveryResume"
            >{{this.jobDetail.deliveryStatus == null?'投递简历':'已投递'}}
            </el-button>
            <div  class="op-container">
              <!-- 在线简历 -->
              <a href="/user/resume" class="resume-online"><i class="el-icon-document"></i>完善在线简历</a>
              <!-- 附件简历 -->
              <a @click="dialogVisible = true" class="resume-file"><i class="el-icon-document"></i>新增附件简历</a>
              <el-dialog
                :visible.sync="dialogVisible"
                width="26%"
              >
                <el-upload
                  class="upload-demo"
                  :action="this.APPConstant.API_HEAD+'/re-user-file/uploadFile'"
                  :data="uploadResumeData"
                  :headers="this.requestHead"
                  :show-file-list="false"
                  :on-success="uploadSuccess"
                  drag
                  multiple>
                  <i class="el-icon-upload"></i>
                  <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                  <div class="el-upload__tip" slot="tip">只能上传doc/pdf文件，且不超过500kb</div>
                </el-upload>
              </el-dialog>
            </div>
          </div>
        </el-col>
      </el-row>
      <el-row class="job-box">
        <el-col :span="18" :offset="3" class="inner">
          <el-row :gutter="50">
            <el-col :span="17" class="job-detail">
              <div class="detail-op">
                <div v-if="isLogin" class="op-link">
                  <a @click="collectionJob()">
                    <i :class="(jobDetail.isCollection==null||jobDetail.isCollection=='0')?'el-icon-star-off':'el-icon-star-on'"></i>
                  </a>
                </div>
                <div class="head">
                  <img src="../../../assets/img.jpg"/>
                </div>
                <h2 class="name">王建国</h2>
                <p>hr . 刚刚在线</p>
              </div>
              <div class="detail-content">
                <div class="job-sec">
                  <h3>职位描述</h3>
                  <div class="text" v-html="jobDetail.jobDescription">
                  </div>
                </div>
                <div class="job-sec company-info">
                  <h3>公司介绍</h3>
                  <div class="text" v-html="jobDetail.companyIntroduction">

                  </div>
                  <a ka="job-comintroduce" :href="'/companyDetail?companyId='+jobDetail.companyId"
                     class="look-all">查看全部</a>
                </div>
                <div class="job-sec">
                  <h3>工商信息</h3>
                  <div class="name">淘宝（中国）软件有限公司</div>
                  <div class="level-list">
                    <li><span>法定代表人：</span>张勇</li>
                    <li><span>注册资金：</span>37500万美元</li>
                    <li class="res-time"><span>成立日期：</span>2004-12-07</li>
                    <li class="company-type"><span>企业类型：</span>有限责任公司（台港澳法人独资）</li>
                    <li class="manage-state"><span>经营状态：</span>存续</li>
                  </div>
                  <a ka="job-cominfo" :href="'/companyDetail?companyId='+jobDetail.companyId" target="_blank" class="look-all">查看全部</a>
                </div>
              </div>
            </el-col>
            <el-col :span="7" class="job-sider">
              <div class="sider-company">
                <p class="title">公司基本信息</p>
                <div class="company-info">
                  <a target="_blank" :href="'/companyDetail?companyId='+this.jobDetail.companyId" >
                    <img :src="jobDetail.imgUrl"/>
                    <span>{{jobDetail.companyName}}</span>
                  </a>
                </div>

                <p><i class="el-icon-c-scale-to-original"></i>{{jobDetail.financingStage}}</p>
                <p><i class="el-icon-user"></i>{{jobDetail.companySize}}</p>
                <p><i class="el-icon-suitcase"></i>{{jobDetail.industry}}</p>
                <p>更新时间 {{jobDetail.updateTime}}</p>
              </div>
            </el-col>
          </el-row>

        </el-col>
      </el-row>
    </el-row>
  </div>

</template>

<script>
  import maintop from "../../../components/maintop";

  export default {
    name: "jobDetail",
    components: {
      'main-top': maintop
    },
    data() {
      return {
        'isLogin': null,
        'jobDetail': null,
        uploadResumeData: {
          'type': '1'
        },
        requestHead: {
          'jwt_access_token': ''
        },
        deliveryBtnIsDisabled:false,
        dialogVisible: false
      }
    },
    methods: {
      collectionJob() {
        var collection = this.jobDetail.isCollection;
        if(collection == null || collection === '0'){
          this.$axios.get(this.APPConstant.API_HEAD+'/re-user-collection-job/collectionJob?jobId='+this.jobDetail.jobId)
            .then(res => {
              if(res.data.code == 0){
                this.jobDetail.isCollection = 'true'
                let elClass = document.getElementsByClassName('el-icon-star-off')[0];
                elClass.className = 'el-icon-star-on'
              }
            })
        }else {
          this.$axios.get(this.APPConstant.API_HEAD+'/re-user-collection-job/collectionJob?jobId='+this.jobDetail.jobId)
            .then(res => {
              if(res.data.code == 0){
                this.jobDetail.isCollection = null
                let elClass = document.getElementsByClassName('el-icon-star-on')[0];
                elClass.className = 'el-icon-star-off'
              }
            })

        }

      },
      uploadSuccess(){
        this.dialogVisible = false;
      },
      deliveryResume(){
        this.deliveryBtnIsDisabled = true;
        this.$axios.get(this.APPConstant.API_HEAD +
          '/re-apply-job-record/collectionJobByType' +
          '?type=1&jobId='+this.jobDetail.jobId+'&companyId='+this.jobDetail.companyId)
          .then(res =>{
            this.getJobDetail();
          })
      },
      addViewd(jobId,companyId){
        this.$axios.get(this.APPConstant.API_HEAD+'/re-apply-job-record/collectionJobByType?type=0&jobId='+jobId+'&companyId='+companyId)
          .then(res =>{
            console.log(res.data)
          })
      },
      getJobDetail(){
        var jobId = this.$route.query.jobId;
        this.$axios.get(this.APPConstant.API_HEAD + '/re-job/jobDetail?jobId=' + jobId)
          .then(res => {
            if(res.data.code == 0){
              if(this.isLogin){
                  if(res.data.data.deliveryStatus == null){
                    this.deliveryBtnIsDisabled = false
                  }else {
                    this.deliveryBtnIsDisabled = true
                  }
                  this.addViewd(res.data.data.jobId,res.data.data.companyId)
              }
              this.jobDetail = res.data.data
            }
          })
      },
       initData() {
        //设置请求头
         if (localStorage.getItem('userDetail')==null){
           this.isLogin = false;
         }else {
           this.requestHead.jwt_access_token = localStorage.getItem(this.APPConstant.JWT_ACCESS_TOKEN)
           this.isLogin = true;
         }
        this.getJobDetail();

      }
    },
    created() {
      this.initData();
    }
  }
</script>

<style scoped>
  a{
    cursor:pointer
  }
  .el-button {
    width: 100%;
    padding: 17px;
    border: none;
  }

  .inner {
    position: relative;
  }

  .job-banner {
    height: auto;
    padding: 24px 0;
    background: #444c60;
    color: #fff;
  }

  .job-banner .info-main {
    width: 650px;
  }

  .job-banner .info-main .job-name {
    display: flex;
    align-items: center;
    padding: 10px 0;
  }

  .job-banner .info-main .job-name h1 {
    font-size: 32px;
    max-width: 380px;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-weight: 400;
    margin-right: 10px;
    line-height: 35px;
    overflow: hidden;
  }

  .job-banner .info-main .job-name .salary {
    color: #fa6a43;
    height: 42px;
    font-size: 25px;
    line-height: 42px;
    font-weight: 600;
  }

  .job-banner .info-main p {
    color: #adb2c1;
    margin-bottom: 10px;
  }

  .job-tags {
    width: 750px;
    margin-top: 5px;
  }

  .job-tags span {
    display: inline-block;
    padding: 0 15px;
    margin-right: 10px;
    margin-bottom: 10px;
    border: 1px solid #5ed5c8;
    color: #5ed5c8;
    height: 23px;
    line-height: 23px;
    border-radius: 13px;
    margin-top: 0;
    vertical-align: top;
    font-size: 12px;
  }

  .job-banner .job-op {
    width: 298px;
    position: absolute;
    right: 0;
    top: 50%;
    bottom: 0;
    margin-top: -47px;
  }

  .job-banner .job-op .op-container {
    overflow: hidden;
    position: absolute;
    bottom: 10px;
    width: 100%;
  }

  .job-banner .job-op .op-container a {
    color: #00d7c6;
    height: 22px;
    line-height: 22px;
    font-size: 16px;
  }

  .job-banner .job-op .op-container .resume-file {
    float: right;
  }


  .job-box {
    margin-top: 30px;
  }


  .job-detail .detail-op {
    float: left;
    width: 100%;
    padding-top: 0;
    margin-bottom: 10px;
  }

  .detail-op .head {
    position: static;
    float: left;
  }

  .detail-op .head img {
    margin-right: 20px;
    width: 60px;
    height: 60px;
    border-radius: 100%;
    /*vertical-align: top;*/
  }

  .detail-op .name {
    margin-top: 5px;
    font-size: 20px;
    color: #424a5e;
    font-weight: 400;
  }

  .detail-op p {
    color: #9fa3af;
  }

  .detail-op .op-link {
    float: right;
    padding-top: 15px;
  }

  .detail-content {
    clear: left;
  }

  .detail-content .job-sec {
    position: relative;
    clear: both;
  }

  .detail-content .job-sec h3 {
    font-weight: 700;
    font-size: 15px;
    color: #424a5e;
    line-height: 50px;
    padding: 0 0 10px 0;
    margin-bottom: 0;
  }

  .job-sec .text, .secretary-sec .text {
    color: #61687c;
    line-height: 36px;
  }

  .job-sec.company-info .text {
    max-height: 150px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .job-sec .look-all {
    color: #5dd5c8;
    float: right;
    margin-top: 10px;

  }

  .level-list {
    margin-top: 5px;
  }

  .level-list li {
    display: inline-block;
    width: 246px;
    margin-top: 5px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .level-list span {
    display: inline-block;
    margin-right: 7px;
    color: #8d92a1;
  }

  .sider-company {

  }

  .sider-company .title {
    line-height: 25px;
    margin-bottom: 25px;
    font-size: 15px;
    font-weight: 500;
    color: #414a60;
  }

  .sider-company .company-info {
    overflow: hidden;
    line-height: 60px;
    margin-bottom: 27px;
  }

  .sider-company img {
    width: 60px;
    height: 60px;
    float: left;
    margin-right: 18px;
    border-radius: 10px;
  }

  .sider-company p {
    line-height: 20px;
    margin-bottom: 26px;
  }

  .sider-company p i {
    margin-right: 25px;
  }
</style>



