<template>
  <div id="jobMain">
    <main-top selected="/jobMain"></main-top>
    <el-row id="filter-box">
      <el-col :span="18" :offset="3" id="search-panel">
        <el-col :span="24" class="inner-search">
          <el-col :span="3">
            <el-cascader
              v-model="searchConditions.workCity"
              :options="cityList"
              :props="{ expandTrigger: 'hover' }"
              :show-all-levels="false"
              placeholder="全国"
              @change="searchJobList(1)"
              clearable
            ></el-cascader>
          </el-col>
          <el-col :span="12">
            <el-input v-model="searchConditions.jobTitle" placeholder='搜索公司、职位'></el-input>
          </el-col>
          <el-col :span="3">
            <el-cascader
              v-model="searchConditions.expectJobs"
              :options="jobTypeList"
              :props="{ expandTrigger: 'hover' }"
              :show-all-levels="false"
              placeholder="职位类型"
              @change="searchJobList(1)"
              clearable
            ></el-cascader>
          </el-col>
          <el-col :span="3">
            <el-select v-model="searchConditions.industry"
                       placeholder="公司行业"
                       @change="searchJobList(1)"
                       clearable
            >
              <el-option
                v-for="(item,index) in industryList"
                :key="index"
                :label="item.dmName"
                :value="item.dmName">
              </el-option>
            </el-select>
          </el-col>
          <el-col :span="3">
            <el-button onmouseenter="this.style.background = '#00d7c6' "
                       onmouseleave="this.style.background = '#00c2b3';this.style.color = '#fff'"
                       @click="searchJobList(1)"
            >搜索

            </el-button>
          </el-col>
        </el-col>
      </el-col>
      <el-col :span="18" :offset="3" class="conditions-box">
        <span>
            {{searchConditions.expectJobs==null?'不限职位':searchConditions.expectJobs}}
            &#12288;|&#12288;
            ({{searchConditions.workCity==null?'全国':searchConditions.workCity}})
        </span>
        <el-row class="condition-region">
          <span :class="regionIndex==-1?'selected':''" @click="replaceRegion(-1,null)">不限</span>
          <span v-for="(region,index) in regionList" :key="index" :class="regionIndex==index?'selected':''"
                @click="replaceRegion(index,region.dmName)">
              {{region.dmName}}
            </span>
        </el-row>
        <el-row class="selected-box">
          <el-col :span="4">
            <el-select v-model="searchConditions.workYear"
                       placeholder="工作经验"
                       @change="searchJobList(1)"
                       clearable
            >
              <el-option
                v-for="(item,index) in workYearList"
                :key="index"
                :label="item.dmName"
                :value="item.dmName">
              </el-option>
            </el-select>
          </el-col>
          <el-col :span="4">
            <el-select v-model="searchConditions.limitEducationBackground"
                       placeholder="学历要求"
                       @change="searchJobList(1)"
                       clearable
            >
              <el-option
                v-for="(item,index) in backgroundList"
                :key="index"
                :label="item.dmName"
                :value="item.dmName">
              </el-option>
            </el-select>
          </el-col>
          <el-col :span="4">
            <el-select v-model="searchConditions.salaryLimit"
                       placeholder="薪资要求"
                       @change="searchJobList(1)"
                       clearable
            >
              <el-option
                v-for="(item,index) in salaryList"
                :key="index"
                :label="item.dmName"
                :value="item.dmName">
              </el-option>
            </el-select>
          </el-col>
          <el-col :span="4">
            <el-select v-model="searchConditions.financingStage"
                       placeholder="融资情况"
                       @change="searchJobList(1)"
                       clearable
            >
              <el-option
                v-for="(item,index) in financingList"
                :key="index"
                :label="item.dmName"
                :value="item.dmName">
              </el-option>
            </el-select>
          </el-col>
          <el-col :span="4">
            <el-select v-model="searchConditions.companySize"
                       placeholder="公司规模"
                       @change="searchJobList(1)"
                       clearable
            >
              <el-option
                v-for="(item,index) in sizeList"
                :key="index"
                :label="item.dmName"
                :value="item.dmName">
              </el-option>
            </el-select>
          </el-col>
          <el-col :span="4">
            <span class="clearCondtions" @click="clearConditions()">清空筛选条件</span>
          </el-col>

        </el-row>
      </el-col>
    </el-row>
    <el-row id="main" :gutter="10">
      <el-col :span="13" :offset="3">
        <el-row class="job-list-box"
                v-loading="isLoading"
                element-loading-text="拼命加载中"
                element-loading-spinner="el-icon-loading"
        >
          <el-row v-for="(jobItem,index) in jobShowData.list" :key="index" class="job-item">
            <el-row class="item-inner">
              <el-col :span="10" :offset="2">
                <div class="job-mess" @click="toJobDetail(jobItem.jobId)">
                  <h3>{{jobItem.jobTitle}}</h3>
                  <div class="append">
                    <span>{{jobItem.minSalary}}~{{jobItem.maxSalary}}</span>
                    <span>{{jobItem.limitEducationBackground}}</span>
                    <span>陈女士|HR</span>
                  </div>
                </div>
              </el-col>
              <el-col :span="10" :offset="2">
                <div class="company-mess" @click="toCompanyDetail(jobItem.companyId)">
                    <h3>{{jobItem.companyName}}</h3>
                    <div class="append">
                      <span>{{jobItem.industry}}</span>
                      <span>{{jobItem.financingStage}}</span>
                      <span>{{jobItem.companySize}}</span>
                    </div>
                    <img :src="jobItem.imgUrl"/>
                </div>
              </el-col>
            </el-row>
            <el-row class="info-append">
              <el-col :span="10" :offset="2">
                <span class="tag-item">数据结构</span>
                <span class="tag-item">数据结构</span>
                <span class="tag-item">数据结构</span>
                <span class="tag-item">数据结构</span>
              </el-col>
              <el-col :span="10" :offset="2" class="info-desc">
                零食下午茶，全勤奖，年终奖，定期体检，员工旅游
              </el-col>
            </el-row>
          </el-row>
          <el-row class="page-box" >
            <el-col v-if="jobShowData.list!=null && jobShowData.list.length!=0"
                    :span="12" :offset="10" class="page-box-inner">
              <el-pagination
                :page-size="searchConditions.size"
                :current-page="searchConditions.current"
                layout="prev, pager, next"
                @current-change="searchJobList"

                :total="searchConditions.total">
              </el-pagination>
            </el-col>
          </el-row>
          <el-row class="data-tips" v-if = "isEmptyJobs">
              <div class="data-blank">
                <i class="tip-errordata"></i>
                <b>没有找到相关职位，修改筛选条件试一下</b>
              </div>
          </el-row>
        </el-row>

      </el-col>
      <el-col :span="5">
        <el-row v-if="userDetail!=null" class="aside-userinfo aside-box">
          <p>看过的职位</p>
                        <div class="my-line" style="background: #DCDFE6;width: 260px;height: 1px;margin-top: 12px;margin-bottom: 12px"></div>
          <div class="watched-jobs">
            <a v-for="(watchedJob,index) in watchJobSList.list" :key="index" target="_blank" :href="'/jobDetail?jobId='+watchedJob.jobId" >
              <h4>
                <span class="job-name">{{watchedJob.jobTitle}}</span>
                <span class="job-salary">{{watchedJob.minSalary}}~{{watchedJob.maxSalary}}</span>
              </h4>
              <p>{{watchedJob.companyName}}</p>
                            <div class="my-line" style="background: #DCDFE6;width: 260px;height: 1px;margin-top: 12px;margin-bottom: 12px"></div>
            </a>
          </div>

        </el-row>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import maintop from "../../../components/maintop";

  export default {
    name: "jobMain",
    components: {
      'main-top': maintop
    },
    data() {
      return {
        userDetail: null,
        searchText: '',
        cityList: [],
        jobTypeList: [],
        industryList: [],
        regionList: [],
        workYearList: [],
        backgroundList: [],
        salaryList: [],
        financingList: [],
        sizeList: [],
        searchConditions: {
          jobTitle: null,
          expectJobs: null,
          industry: null,
          workCity: null,
          region: null,
          workYear: null,
          limitEducationBackground: null,
          salaryLimit: null,
          financingStage: null,
          companySize: null,
          current: 1,
          size: 10,
          total: null,
          type: 1 //1：默认查询无排序，2：按发布时间排序
        },
        jobShowData: null,
        watchJobSList: [],
        jobRecommendListType: 1,
        regionIndex: -1,
        isLoading: false,
        isEmptyJobs: false
      }
    },
    methods: {
      replaceRegion(index, region) {
        this.regionIndex = index;
        this.searchConditions.region = region;
        this.searchJobList(1)
      },
      clearConditions() {
        var searchConditions = this.searchConditions;
        for (var val in searchConditions) {
          if (val != 'current' && val != 'size' && val != 'type') {
            searchConditions[val] = '';
          }
        }
        this.searchConditions.workCity = null
        this.searchConditions.expectJobs = null
        this.regionIndex = -1;
        this.searchJobList(1);
      },
      jobRecommendSelect(key, keyPath) {
        this.jobRecommendListType = key;
      },
      toJobDetail(jobId) {
        let routeUrl = this.$router.resolve('/jobDetail?jobId=' + jobId);
        window.open(routeUrl.href, '_blank');
      },
      toCompanyDetail(companyId) {
        let routeUrl = this.$router.resolve('/companyDetail?companyId=' + companyId)
        window.open(routeUrl.href, '_blank');
      },
      //查询职位列表
      searchJobList(param) {
        this.searchConditions.current = param;
        this.isLoading = true;
        this.jobShowData = {};
        if (this.searchConditions.workCity instanceof Array) {
          this.searchConditions.workCity = this.searchConditions.workCity[1];
        }
        if (this.searchConditions.expectJobs instanceof Array) {
          this.searchConditions.expectJobs = this.searchConditions.expectJobs[1];
        }
        this.$axios.post(this.APPConstant.API_HEAD + '/re-job/searchJob', this.searchConditions)
          .then(res => {
            this.jobShowData = res.data.data
            this.searchConditions.current = res.data.data.pageNum;
            this.searchConditions.total = res.data.data.total
            this.isLoading = false
            if(this.jobShowData.list.length == 0){
              this.isEmptyJobs = true
            }else {
              this.isEmptyJobs = false
            }
          })
        //所选市的对应区列表
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataSimple?dmName=' + this.searchConditions.workCity)
          .then(res => {
            this.regionList = res.data.data
          })

      },
      //查询浏览过的职位列表
      getViewJobData(param){
        this.$axios.get(this.APPConstant.API_HEAD +
          '/re-apply-job-record/getRecordJobByType' +
          '?current='+param+'&size=5'+'&type=0')
          .then(res => {
            this.watchJobSList = res.data.data
          })
      },
      //初始化参数
      initData() {
        //获取用户详细信息
        this.userDetail = JSON.parse(localStorage.getItem('userDetail'));

        //是否带搜索关键字
        if(this.$route.query.keyword!=null){
          this.searchConditions.jobTitle = this.$route.query.searchText==''?null:this.$route.query.searchText
          this.searchConditions.expectJobs = this.$route.query.jobType==''?null:this.$route.query.jobType
        }
        //获取用户期望工作城市
        if (this.userDetail != null){
          this.searchConditions.workCity = this.userDetail.workCity
          this.searchConditions.expectJobs = this.userDetail.expectJobs
        }

        //城市下拉值
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataCascade?fatherId=30012')
          .then(res => {
            this.cityList = res.data.data
          })
        //职位类型下拉值
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataCascade?fatherId=40001')
          .then(res => {
            this.jobTypeList = res.data.data
          })
        //行业下拉值
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataSimple?fatherId=40003')
          .then(res => {
            this.industryList = res.data.data
          })
        //所选市的对应区列表
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataSimple?dmName=' + this.searchConditions.workCity)
          .then(res => {
            this.regionList = res.data.data
          })
        //工作经验下拉
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataSimple?fatherId=40004')
          .then(res => {
            this.workYearList = res.data.data
          })
        //学历要求下拉
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataSimple?fatherId=40005')
          .then(res => {
            this.backgroundList = res.data.data
          })
        //薪资范围下拉
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataSimple?fatherId=40006')
          .then(res => {
            this.salaryList = res.data.data
          })
        //融资阶段下拉
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataSimple?fatherId=40007')
          .then(res => {
            this.financingList = res.data.data
          })
        //公司规模下拉
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataSimple?fatherId=40008')
          .then(res => {
            this.sizeList = res.data.data
          })
        //初始化职位列表
        this.searchJobList(1);
        //初始化浏览记录列表
        if(this.userDetail!=null){
          this.getViewJobData(1);

        }

      }

    },
    created() {
      this.initData();
    }
  }
</script>

<style scoped>
  #jobMain {
    background: #f6f6f8;
  }

  a:hover {
    color: #00c2b3;
  }

  .el-button {
    width: 100%;
    color: #fff;
    background: #00c2b3;
    padding: 17px;
  }

  #filter-box {
    background: #fff;
  }

  #search-panel {
    margin-bottom: 16px;
    margin-top: 20px;
  }

  .inner-search {
    background: white;

    height: 45px;
    box-shadow: 1px 1px 6px #909090;
    display: flex;
    align-items: center;
  }

  /deep/ .el-input__inner {
    border: none;
  }

  .conditions-box {
    font-size: 12px;
    padding: 5px 0;
    background: #fff;
  }

  .conditions-box > span {
    display: inline-block;
    font-weight: 500;
    font-size: 14px;
    color: #51586d;
    line-height: 38px;
  }

  .condition-region {

  }

  .condition-region > span {
    display: inline-block;
    height: 35px;
    margin-right: 22px;
    line-height: 35px;
    color: #414a60;
    cursor: pointer;
    white-space: nowrap;
  }

  .condition-region .selected {
    color: #fc6c38;
  }

  .condition-region span:hover {
    color: #fc6c38;
  }

  .selected-box {
    display: flex;
    align-items: center;
    /*height: 500px;*/
  }

  .selected-box .clearCondtions {
    cursor: pointer;
  }

  .selected-box .clearCondtions:hover {
    color: #00d7c6;
  }

  #main {
    margin-top: 15px;
  }

  .item-inner {
    background: #fff;
    margin-bottom: 5px;
    padding: 25px 0 20px;
  }

  .job-mess, .company-mess {
    cursor: pointer
  }


  .job-item h3 {
    /*color: #00c2b3;*/
    max-width: 300px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .job-mess:hover {
    color: #00d7c6;
  }

  .company-mess:hover {
    color: #00d7c6;
  }


  .job-item .append {
    width: 60%;
    margin-top: 5px;
    font-size: 12px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    -o-text-overflow: ellipsis;
  }

  .job-item img {
    position: absolute;
    top: 16px;
    right: 20px;
    width: 54px;
    height: 54px;
    border: 1px solid #f3f5fb;
    border-radius: 9px;
  }

  .job-item .info-append {
    padding: 5px 0 10px;
  }

  .job-item .info-append span {
    font-size: 12px;
    height: 21px;
    line-height: 21px;
    padding: 0 8px;
    border: 1px solid #eee;
    border-radius: 2px;
    display: inline-block;
    margin-right: 10px;
  }

  .job-item .info-append .info-desc {
    font-size: 13px;
  }

  .page-box {
    background: #fff;
    padding: 10px;
    min-height: 60px;
  }

  .aside-box {
    background: #fff;
    /*margin-top: 20px;*/
    padding: 20px 30px;
    margin-bottom: 16px;
  }


  .aside-userinfo {
    position: relative;
  }


  .watched-jobs a {
    color: #414a60;
  }

  .watched-jobs a:hover {
    color: #00c2b3;
  }

  .watched-jobs a p {
    font-size: 10px;
  }


  .watched-jobs h4 {
    font-weight: 400;
    padding-right: 90px;
    position: relative;
    font-size: 14px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    -o-text-overflow: ellipsis;
    min-height: 22px;
  }


  .watched-jobs .job-salary {
    color: #fc6c38;
    position: absolute;
    right: 0;
  }

  .data-tips {
    text-align: center;
    min-height: 340px;
    position: relative;
    padding: 100px 0;
    background: #fff;
  }

  .data-tips .data-blank {

    width: 360px;
    margin: 0 auto;
    text-align: center;
  }

  .tip-errordata {
    display: block;
    width: 412px;
    height: 212px;
    margin: -20px auto 15px;
    background: url('https://static.zhipin.com/zhipin-geek/v383/web/geek/images/tip-errordata.png') 90px 0 no-repeat;
  }



</style>



