<template>
  <el-row id="companyTop">
    <el-col :span="18" :offset="3" class="inner-top">
      <el-col :span="3" class="logo">
        <a href="https://www.zhipin.com/" ka="header-home-logo" title="BOSS直聘"><span>慧眼直聘企业端</span></a>
      </el-col>
      <el-col :span="3" :offset="18">
        <el-menu default-active="1"
                 background-color="#202329"
                 text-color="#fff"
                 mode="horizontal"
                 @select="menuAction"
        >
          <el-submenu index="1">
            <template slot="title">华为技术有限公司</template>
            <el-menu-item index="1-1">修改密码</el-menu-item>
            <el-menu-item index="1-2">退出</el-menu-item>
          </el-submenu>
        </el-menu>
      </el-col>
    </el-col>
  </el-row>
</template>

<script scoped>
  export default {
    name: "companyTop",
    data() {
      return {
        name: null
      };
    },
    methods: {
      toLoginPage() {
        this.$router.push('/login')
      },
      menuAction(key, keyPath) {
        if (key === '1-2') {
          this.$axios.get(this.APPConstant.API_HEAD+'/re-company-boss/loginOut')
          .then(res => {
            if (res.data.code === 0){
              localStorage.removeItem(this.APPConstant.ACCESS_BOSS_TOKEN)
              localStorage.removeItem('bossDetail')
              this.$router.push('/boss/bossLogin') // 跳转到B
            }
          })
        }

      },
      initData() {

      }

    },
    mounted() {
      this.initData()
    }
  }
</script>

<style scoped>
  #companyTop {
    color: #fff;
    background: #202329;
  }

  .inner-top {
    height: 60px;
    display: flex;
    align-items: center;
  }

  .el-button {
    color: #fff;
    background: #00c2b3;
    border: none;
  }


  #top a {
    color: #fff;
    text-decoration: none;
  }

  .logo span {
    font-family: 微软雅黑;
    font-weight: bold;
    font-size: 20px;
    color: #00d7c6;
  }

</style>



