<template>
  <div id="applyDetail">
    <company-top></company-top>
    <el-row id="controller" :gutter="30">
      <el-col :span="13" :offset="3">
        <div class="resume-content-box">
          <div class="resume-box">

            <div class="op">
              <span class="statusText">{{statusText}}</span>
              <el-button type="success" :disabled="opBtnDisabled">
                {{opBtnText}}
              </el-button>
            </div>

            <div class="resume-item resume-userinfo" v-if="userDetail.usermainmessage!=null">
              <div class="userinfo">
                <div class="head-box ">
                  <img src="../../assets/img4.jpg" class="head-img"/>
                </div>
                <p class="name">{{userDetail.usermainmessage.name}}</p>
              </div>
              <div class="info-detail">
              <span>
                <i class="el-icon-document"></i>
                21年应届生
              </span>
                &#12288;|&#12288;
                <span>
                <i class="el-icon-school"></i>
                {{userDetail.usermainmessage.highestEducationBackground}}
              </span>
                &#12288;|&#12288;
                <span>

                <i class="el-icon-user"></i>
                {{userDetail.usermainmessage.jobStatus}}
              </span>
              </div>
              <div class="info-detail">
              <span>
                <i class="el-icon-phone-outline"></i>
                {{userDetail.usermainmessage.telephoneNumber}}
              </span>
                &#12288;|&#12288;
                <span>
                <i class="el-icon-chat-dot-round"></i>
                {{userDetail.usermainmessage.wechatNumber}}
              </span>
              </div>
            </div>

            <div class="resume-item summary" v-if="userDetail.usermainmessage!=null">

              <h3 class="title">|&#12288;个人优势</h3>
              <div class="resume-edit-advantage comment-hover">
                <div class="advantage-text" v-html="userDetail.usermainmessage.personalAdvantage">

                </div>
              </div>
            </div>

            <div class="resume-item expect-job" v-if="userDetail.usermainmessage!=null">
              <h3 class="title">|&#12288;求职期望</h3>
              <div class="expect-message comment-hover">
                <i class="el-icon-shopping-bag-1"></i>
                &#12288;
                {{userDetail.usermainmessage.expectJobs}}
                &#12288;|&#12288;
                <i class="el-icon-money"></i>
                {{userDetail.usermainmessage.minSalary}}-{{userDetail.usermainmessage.maxSalary}}
                &#12288;|&#12288;
                <i class="el-icon-suitcase"></i>
                {{userDetail.usermainmessage.expectIndustry}}
                &#12288;|&#12288;
                <i class="el-icon-location-outline"></i>
                {{userDetail.usermainmessage.workCity}}
              </div>
            </div>

            <div class="resume-item internship" v-if="userDetail.internshipData!=null">
              <h3 class="title">
                |&#12288;实习经历
              </h3>
              <div v-for="(internshipItem,index) in userDetail.internshipData" :key="index"
                   class="comment-item comment-hover">
                <div class="info-text">
                  <h4 class="name">{{internshipItem.companyName}}</h4>
                  &#12288;
                  <span class="period">{{internshipItem.startTime+'-'+internshipItem.endTime}}</span>
                </div>
                <h4>
                  <span>{{internshipItem.companyDepartment}}</span>
                  &#12288; &#12288;| &#12288;
                  <span>{{internshipItem.jobName}}</span>
                  &#12288;
                  <span v-show="internshipItem.isInternship == '1'" class="internship-tag">实习</span>
                </h4>
                <div class="info-text">
                  <span class="text-content">内容:</span>
                  <span class="text-box" v-html="internshipItem.workContent">
                  </span>
                </div>

                <div class="ability-tag">
                  <span>mysql</span>
                  <span>springBoot</span>
                  <span>redis</span>
                </div>


              </div>

            </div>

            <div class="resume-item project-experience" v-if="userDetail.projectExperienceData!=null">
              <h3 class="title">
                |&#12288;项目经历
              </h3>

              <div v-for="(projectItem,index) in userDetail.projectExperienceData" :key="index"
                   class="comment-item comment-hover">
                <div class="info-text">
                  <h4 class="name">{{projectItem.projectName}}</h4>
                  &#12288;
                  <span class="period">{{projectItem.projectStartTime+'-'+projectItem.projectEndTime}}</span>
                </div>
                <h4>
                  {{projectItem.projectRole}}
                </h4>
                <div class="info-text">
                  <span class="text-content">内容:</span>
                  <span class="text-box" v-html="projectItem.projectDescription">
                  </span>
                </div>

                <div class="ability-tag">
                  <span>mysql</span>
                  <span>springBoot</span>
                  <span>redis</span>
                </div>
              </div>

            </div>

            <div class="resume-item education-experience" v-if="userDetail.educationData!=null">
              <h3 class="title">
                |&#12288;教育经历
              </h3>
              <div v-for="(educationExperienceItem,index) in userDetail.educationData" :key="index"
                   class="comment-item comment-hover">
                <div class="info-text">
                  <h4 class="name">{{educationExperienceItem.schoolName}}</h4>
                  &#12288;
                  <span class="period">{{educationExperienceItem.startTime+'-'+educationExperienceItem.endTime}}</span>
                </div>
                <div class="info-text">
                  <h4>
                    {{educationExperienceItem.major}}
                    &#12288; |&#12288;
                    {{educationExperienceItem.educationalBackground}}
                  </h4>
                </div>

                <div class="info-text">
                  <span class="text-content">在校经历:</span>
                  <span class=" education-text" v-html="educationExperienceItem.schoolExperience">
                  </span>
                </div>
              </div>

            </div>

            <div class="resume-item certificate" v-if="userDetail.certificateData!=null">
              <h3 class="title">|&#12288;资格证书</h3>
              <div class="comment-item comment-hover">
                <div class="ability-tag">
                  <span v-for="(certificateItem,index) in userDetail.certificateData" :key="index">
                    {{certificateItem}}
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </el-col>
      <el-col :span="5" class="user-aside" v-if="userFile!=null">
        <div class="aside-resume aside-box">

          <div class="document-file">
            <p>附件管理</p>
            <div class="file-main">
              <div v-for="(resumeItem,index) in userFile.resumeList" :key="index" class="file-item">
                <i class="el-icon-document"></i>
                <a :href="resumeItem.fileUrl" target="_blank">
                  <span>{{resumeItem.fileName}}</span>
                </a>
              </div>
            </div>
          </div>
        </div>
        <div class=" aside-box">

        </div>
        <div class=" aside-box">

        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import companyTop from "../../components/companyTop";

  export default {
    name: "applyDetail",
    components: {
      "company-top": companyTop,
    },
    data() {
      return {
        userId: '',
        jobId: '',
        userDetail: {},
        deliveryStatus: {},
        userFile: null,
        statusText: '已投递简历',
        opBtnText: '邀约面试',
        opBtnDisabled: false,
        formLabelWidth: '120px',
      }
    },
    methods: {
      getUserDetailAll() {
        this.$axios.get(this.APPConstant.API_HEAD + '/re-user-detail/getApplyUserDetailAll?userId='+this.userId)
          .then(res => {
            this.userDetail = res.data.data;
          })
      },
      getAllUserFile() {
        this.$axios.get(this.APPConstant.API_HEAD + '/re-user-file/getApplyUserFile?userId='+this.userId)
          .then(res => {
            this.userFile = res.data.data
          })
      },
      getStatus(){
        this.$axios.get(this.APPConstant.API_HEAD + '/re-company-boss/getDelivererStatus' +
          '?userId='+this.userId +'&jobId='+this.jobId)
          .then(res => {
            this.deliveryStatus = res.data.data;
            let status = this.deliveryStatus.status;
            switch (status) {
              case '1':{
                this.statusText = '已投递简历';
                this.opBtnText = '邀约面试';
                this.opBtnDisabled = false;
                break;
              }
              case '2':{
                this.statusText = '面试中';
                this.opBtnText = '通过面试';
                this.opBtnDisabled = false;
                break;
              }
              case '3':{
                this.statusText = '面试已完成';
                this.opBtnText = '已完成';
                this.opBtnDisabled = true;
                break;
              }
            }
          })
      },
      initData() {
        this.userId = this.$route.query.userId;
        this.jobId = this.$route.query.jobId
        this.getUserDetailAll();
        this.getStatus();
        this.getAllUserFile();
      }
    },
    created() {
      this.initData();
    }
  }
</script>

<style scoped>


  #applyDetail {
    background: #f6f6f8;
  }

  a {
    cursor: pointer
  }

  a:hover {
    color: #00c2b3;
  }

  .resume-item .comment-hover:hover {
    background: #f6f6f8;
  }

  .resume-content-box {
    padding: 30px 40px 0;
    background: #fff;
    margin-top: 20px;
  }

  .resume-item {
    position: relative;
    padding: 20px 0;
  }

  .userinfo {
    display: flex;
    align-items: center;
  }

  .head-box {
    width: 56px;
    height: 56px;
  }

  .head-box .head-img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }

  .resume-userinfo{
    position: relative;
    z-index: 0;
  }

  .resume-userinfo .name {
    color: #24272e;
    margin-left: 20px;
    font-size: 24px;
  }


  .info-detail {
    margin-top: 15px;
  }


  .resume-edit-advantage {
    position: relative;
  }

  .resume-item .title {
    margin-bottom: 20px;
    position: relative;
  }

  .resume-item .title a {
    position: absolute;
    right: 5px;
    font-size: 14px;
    font-weight: 400;
    color: #00c2b3;
  }

  .resume-item .title a:hover {
    color: #00d7c6;
  }

  .resume-edit-advantage .advantage-text {
    padding: 8px 0 8px 20px;
    width: 80%;
  }

  .expect-job .expect-message {
    padding: 8px 0 8px 20px;
    position: relative;
    min-height: 20px;
  }

  .expect-job .expect-message .op {
    top: 8px;
  }

  .comment-item {
    padding: 8px 0 8px 20px;
    position: relative;
    min-height: 40px;
  }

  .comment-item .info-text {
    padding: 10px 0;
  }

  .comment-item h4 {
    font-size: 15px;
    font-weight: 700;
  }

  .comment-item .name {
    max-width: 420px;
    word-break: break-all;
    float: left;
    color: #414a60;
  }

  .comment-item .period {
    font-size: 12px;
    padding-left: 30px;
    float: left;
    position: relative;
    top: 2px;
  }

  .comment-item .internship-tag {
    width: 44px;
    height: 20px;
    line-height: 20px;
    border-radius: 10px;
    border: 1px solid #3a98fc;
    margin-left: 20px;
    font-size: 12px;
    font-weight: 500;
    color: #3a98fc;
    display: inline-block;
    text-align: center;
  }

  .comment-item .text-content {
    font-weight: 700;
  }

  .comment-item .info-text .text-box {
    position: relative;
    left: 60px;
    top: -18px;
    display: block;
    width: 90%;
  }

  .education-experience .education-text {
    position: relative;
    left: 80px;
    top: -18px;
    display: block;
    width: 90%;

  }

  .comment-item .ability-tag {
    padding: 7px 0 5px;
  }

  .comment-item .ability-tag span {
    display: inline-block;
    font-size: 12px;
    line-height: 20px;
    color: #9fa3b0;
    padding: 0 14px;
    margin-right: 10px;
    border: 1px solid #cfd1d7;
    border-radius: 50px;
  }


  .aside-box {
    background: #fff;
    margin-top: 20px;
    padding: 20px 30px;
    margin-bottom: 16px;
  }

  .file-main {
    margin-top: 10px;
  }

  .file-item {
    position: relative;
    color: #414a60;
    font-size: 14px;
    cursor: default;
    margin-top: 10px;
  }

  .op{
    position: relative;
    float: right;
    z-index: 9999;
  }
  .op .statusText{
    color: #fc6c38;
    margin-right: 20px;
  }

</style>



