<template>
  <el-container>
    <el-header>
      <company-top></company-top>
    </el-header>
    <el-container>
      <el-aside width="200px" style="background: whitesmoke;" class="aside">
        <company-aside active="2"></company-aside>
      </el-aside>
      <el-main>
        <div class="main-box">
          <el-form :inline="true" :model="companyDetail" class="form-item">
            <el-form-item label="公司名称">
              <el-input v-model="companyDetail.companyName" placeholder="公司名称"></el-input>
            </el-form-item>
            <el-form-item label="所在市">
              <el-cascader v-model="companyDetail.city"
                           :options="cityList"
                           :props="{ expandTrigger: 'hover' }"
                           :show-all-levels="false"
                           clearable>
              </el-cascader>
            </el-form-item>
            <el-form-item label="所在区">
              <el-select placeholder="选择区" v-model="companyDetail.region">
                <el-option v-for="(regionItem,index) in regionList" :key="index"
                           :label="regionItem.dmName" :value="regionItem.dmName">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="详细地址">
              <el-input placeholder="编辑详细地址" v-model="companyDetail.address"></el-input>
            </el-form-item>
            <el-form-item label="联系电话">
              <el-input placeholder="编辑电话" v-model="companyDetail.telephone"></el-input>
            </el-form-item>
            <el-form-item label="所属行业">
              <el-select placeholder="选择行业" v-model="companyDetail.industry">
                <el-option v-for="(industryItem,index) in industryList" :key="index"
                           :label="industryItem.dmName" :value="industryItem.dmName">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="融资阶段">
              <el-select placeholder="选择融资阶段" v-model="companyDetail.financingStage">
                <el-option v-for="(financingItem,index) in financingList" :key="index"
                           :label="financingItem.dmName" :value="financingItem.dmName">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="公司规模">
              <el-select placeholder="选择公司规模" v-model="companyDetail.companySize">
                <el-option v-for="(sizeItem,index) in sizeList" :key="index"
                           :label="sizeItem.dmName" :value="sizeItem.dmName">
                </el-option>
              </el-select>
            </el-form-item>
          </el-form>
          <div class="text-box">
            <el-form>
              <el-form-item label="公司简介">
                <mavon-editor
                  v-model="companyIntroductionMarkDown"
                  :toolbars="this.APPConstant.toolbars"
                  @change="changeCompanyIntroduction"
                  defaultOpen='edit'
                  @keydown=""
                />
              </el-form-item>
              <el-form-item label="产品介绍">
                <mavon-editor
                  v-model="productIntroductionMarkDown"
                  @change="changeProductIntroduction"
                  :toolbars="this.APPConstant.toolbars"
                  defaultOpen='edit'
                  @keydown=""
                />
              </el-form-item>
              <el-form-item label="公司福利">
                <mavon-editor
                  v-model="companyWelfareMarkDown"
                  @change="changeCompanyWelfare"
                  :toolbars="this.APPConstant.toolbars"
                  defaultOpen='edit'
                  @keydown=""
                />
              </el-form-item>
            </el-form>
          </div>
          <div class="submit">
            <el-button type="primary" @click="commitUpdate" size="medium">提交信息</el-button>
            <el-button size="medium" @click="getCompanyDetail">重置信息</el-button>

          </div>
        </div>


      </el-main>
    </el-container>

  </el-container>
</template>

<script>
  import companyTop from "../../components/companyTop";
  import companyAside from "../../components/companyAside";
  import "mavon-editor/dist/css/index.css";
  import companyDetail from "../recruitment/detail/companyDetail";

  export default {
    name: "editCompanyPage",
    components: {
      "company-top": companyTop,
      "company-aside": companyAside

    },
    data() {
      return {
        companyDetail: {

        },
        companyIntroductionMarkDown: '',
        productIntroductionMarkDown: '',
        companyWelfareMarkDown: '',
        cityList: null,
        regionList: null,
        industryList: null,
        financingList: null,
        sizeList: null
      }
    },
    methods: {
      commitUpdate(){
        this.$axios.post(this.APPConstant.API_HEAD+'/re-company-boss/updateCompanyDetail',this.companyDetail)
          .then(res => {
            if (res.data.code == 0){
              this.$alert('修改成功！', '提示', {
                confirmButtonText: '确定',
                callback: action => {
                  this.getCompanyDetail()
                }
              });
            }
          })
      },
      changeCompanyIntroduction(value, render) {
        this.companyDetail.companyIntroduction = render;
      },
      changeProductIntroduction(value, render) {
        this.companyDetail.productIntroduction = render;
      },
      changeCompanyWelfare(value, render) {
        this.companyDetail.companyWelfare = render;
      },
      getRegionData(city) {
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataSimple?dmName=' + city)
          .then(res => {
            this.regionList = res.data.data
          })
      },
      getDropData() {
        //城市下拉值
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataCascade?fatherId=30012')
          .then(res => {
            this.cityList = res.data.data
          })
        //行业下拉值
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataSimple?fatherId=40003')
          .then(res => {
            this.industryList = res.data.data
          })
        //融资阶段下拉
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataSimple?fatherId=40007')
          .then(res => {
            this.financingList = res.data.data
          })
        //公司规模下拉
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataSimple?fatherId=40008')
          .then(res => {
            this.sizeList = res.data.data
          })
      },
      getCompanyDetail(){
        const TurndownService = require('turndown').default
        var turndownService = new TurndownService();
        this.$axios.get(this.APPConstant.API_HEAD + '/re-company-boss/getCompanyDetail')
          .then(res => {
            this.companyDetail = res.data.data;
            if(this.companyDetail.companyIntroduction !=null){
              this.companyIntroductionMarkDown
                = turndownService.turndown(this.companyDetail.companyIntroduction);
            }
           if(this.companyDetail.productIntroduction !=null){
             this.productIntroductionMarkDown
               = turndownService.turndown(this.companyDetail.productIntroduction);
           }
           if(this.companyDetail.companyWelfare !=null){
             this.companyWelfareMarkDown
               = turndownService.turndown(this.companyDetail.companyWelfare);
           }
            this.getRegionData(this.companyDetail.city)
          })
      },
      initData() {
        this.getCompanyDetail()
        this.getDropData()
      }
    },
    created() {
      this.initData();

    }
  }
</script>

<style scoped>
  .el-form--inline .el-form-item {

    margin-right: 100px;

  }

  .el-container {
    width: 100%;
    height: 700px;
  }

  .el-header {
    padding: 0;
  }

  .el-aside {
    height: 100%;
  }


  .main-box {
    width: 90%;
    margin-left: 50px;
  }

  .main-box .submit {
    width: 19%;
    margin: 0 auto;
  }


</style>



