                  <img :src="jobItem.imgUrl" height="400" width="600"/></a>
                <a :href="'/companyDetail?companyId='+jobItem.companyId">
                  <span>{{jobItem.companyName}}</span>
                </a>

                <div class="commpany-place">
                  <i class="el-icon-location-outline"></i>
                  <span class="address-text">{{jobItem.region}}</span>
                </div>
              </div>
            </div>

          </el-col>

        </el-row>

        <el-col :span="6" :offset="9">
          <el-button onmouseenter="this.style.background = '#00d7c6' "
                     onmouseleave="this.style.background = '#00c2b3';this.style.color = '#fff' "
                     @click="$router.push('/jobMain')"
          >查看更多
          </el-button>
        </el-col>


      </el-col>

      <el-col :span="18" :offset="3" class="job-hot common-box">
        <div class="box-title">
          <div class="before"></div>
          热招职位
          <div class="after"></div>
        </div>

        <el-row class="box-nav">
          <el-col :span="20" :offset="3">
            <el-menu default-active="0" mode="horizontal" @select="jobHotSelect">
              <el-menu-item index="0">互联网技术</el-menu-item>
              <el-menu-item index="1">金融</el-menu-item>
              <el-menu-item index="2">房地产·建筑</el-menu-item>
              <el-menu-item index="3">教育培训</el-menu-item>
              <el-menu-item index="4">娱乐传媒</el-menu-item>
              <el-menu-item index="5">医疗健康</el-menu-item>
              <el-menu-item index="6">法律咨询</el-menu-item>
              <el-menu-item index="7">供应链·物流</el-menu-item>
              <el-menu-item index="8">人力行政</el-menu-item>
            </el-menu>
          </el-col>
        </el-row>

        <el-row :gutter="20" class="box-main"v-for="(jobPartList,index1) in hotJobData" :key="index1" v-show="jobHotType == index1">
          <el-col v-for="(jobItem,index2) in jobPartList" :key="index2" :span="8">
            <div class="job-item">
              <a :href="'/jobDetail?jobId='+jobItem.jobId" class="item-info">
                <div class="info-top">
                  <p class="name">{{jobItem.jobTitle}}</p>
                  <p class="salary">{{jobItem.minSalary}}~{{jobItem.maxSalary}}.{{jobItem.salaryNums}}薪</p>
                </div>
                <p class="job-text">{{jobItem.limitEducationBackground==null?'学历不限':jobItem.limitEducationBackground}}</p>
              </a>

              <div class="my-line" style="background: #DCDFE6;width: 320px;height: 1px;margin-top: 12px;margin-bottom: 12px"></div>

              <div class="item-bottom">
                <a :href="'/companyDetail?companyId='+jobItem.companyId">
                  <img :src="jobItem.imgUrl" height="400" width="600"/></a>
                <a :href="'/companyDetail?companyId='+jobItem.companyId">
                  <span>{{jobItem.companyName}}</span>
                </a>

                <div class="commpany-place">
                  <i class="el-icon-location-outline"></i>
                  <span class="address-text">海珠区·客村</span>
                </div>
              </div>
            </div>

          </el-col>
        </el-row>


        <el-col :span="6" :offset="9">
          <el-button onmouseenter="this.style.background = '#00d7c6' "
                     onmouseleave="this.style.background = '#00c2b3';this.style.color = '#fff' "
                     @click="$router.push('/jobMain')"
          >查看更多
          </el-button>
        </el-col>
      </el-col>

