          <el-col v-for="(companyItem,index) in hotCompanyData" :span="6" :key="index">
            <div class="company-item">
              <div class="company-info">
                <a :href="'/companyDetail?companyId='+companyItem.companyId">
                  <div class="img-box">
                    <img :src="companyItem.imgUrl" height="58" width="58"/>
                  </div>
                  <div class="conpany-text">
                    <h4>{{companyItem.companyName}}</h4>
                    <p>
                      {{companyItem.financingStage}} | {{companyItem.industry}}
                    </p>
                  </div>
                </a>

              </div>
              <div class="about-info">
                <a href="#">
                  <p>
                    <span>{{companyItem.jobNums}}</span>
                    个热招职位
                  </p>
                </a>
              </div>

            </div>

          </el-col>
        </el-row>


