</template>

<script>
  import maintop from "../../../components/maintop";

  export default {
    name: "mainPage",
    components: {
      'main-top': maintop
    },
    data() {
      return {
        userDetail: null,
        searchText: '',
        jobType: [null,''], //搜索框的职位类别下拉选中值
        jobTypeList: [],
        imgList: [
          {
            url: require("@/assets/img.jpg")
          },
          {
            url: require("@/assets/img1.jpg")
          },
          {
            url: require("@/assets/img2.jpg")
          },
          {
            url: require("@/assets/img3.jpg")
          }
        ],
        industryList:[],
        hoverIndex: -1,
        jobData: {},
        hotJobData: [],
        hotCompanyData: [],
        searchHotJobData: [],
        jobRecommendListType: 1,
        jobHotType: 0
      }
    },
    methods: {
      jobRecommendSelect(key, keyPath) {
        this.jobRecommendListType = key;
      },
      jobHotSelect(key) {
        this.jobHotType = key;
      },
      getUserMess() {
        this.userDetail = JSON.parse(localStorage.getItem('userDetail'));
      },
      getRecommendJobDatas(){
        this.$axios.get(this.APPConstant.API_HEAD+'/re-job/mainPageRecommend')
          .then(res => {
            this.jobData = res.data.data
          })
      },
      getHotJobDatas(){
        this.$axios.get(this.APPConstant.API_HEAD+'/re-job/jobHot')
          .then(res => {
            this.hotJobData = res.data.data
          })
      },
      getHotCompanyDatas(){
        this.$axios.get(this.APPConstant.API_HEAD+'/re-company/hotCommend')
          .then(res => {
            this.hotCompanyData = res.data.data
          })
      },
      getDropdownValues(){
        this.$axios.get(this.APPConstant.API_HEAD+'/gy-base-dmcode/getDataCascade?fatherId=40001')
          .then(res => {
            this.jobTypeList = res.data.data
          })
      },
      getSearchHotJob(){
        this.$axios.get(this.APPConstant.API_HEAD+'/gy-base-dmcode/getDataSimple?fatherId=40009')
          .then(res => {
            this.searchHotJobData = res.data.data
          })
      },
      toJobMainPage(){
        this.$router.push('/jobMain?keyword=true&searchText='+this.searchText+'&jobType='+this.jobType[1])
      },
      getIndustryList(){
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataSimple?fatherId=40003')
          .then(res => {
            this.industryList = res.data.data
          })
      }
    },
    mounted() {
      this.getDropdownValues();
      this.getIndustryList();
      this.getHotJobDatas();
      this.getHotCompanyDatas();
      this.getSearchHotJob();
      if(localStorage.getItem('userDetail')!=null){
        this.getUserMess();
        this.getRecommendJobDatas();
      }
    }
  }
</script>

<style scoped>

  button span {
    color: #fff;
  }

  #mainPage {
    background: #f6f6f8;
  }

  #search-panel {
    /*height: 131px;*/
  }

  .inner-search {
    background: white;
    margin-top: 20px;
    height: 50px;
    box-shadow: 1px 1px 6px #909090;
    display: flex;
    align-items: center;
  }

  /deep/ .el-input__inner {
    border: none;
  }


  .el-button {
    width: 101%;
    color: #fff;
    background: #00c2b3;
    padding: 17px;
  }

  .search-hot {
    margin-top: 20px;
    height: 41px;
    font-size: 13px;
  }

  .search-hot a {
    margin-left: 22px;
    color: #00c2b3;

  }

  .home-box {

  }

  .home-asider {
    position: relative;
    height: 325px;
    background: #fff;
  }





  .home-main {
    height: 325px;
    background: #fff;
  }


  .home-asider .main-btn{
    padding-top: 5px;
    position: relative;
    margin: 0 auto;
    width:100px;
  }
  .job-menu  p{
    font-size: 15px;
    font-weight: 600;
    float: left;
  }
  .job-menu  span{
    margin-left: 10px;
    line-height: 28px;
  }

  .job-menu .menu-item{
    display: flex;
    align-items: center;
    padding: 7px 20px;
    color: #414a60;
  }
  .job-menu .hover-item{
    display: flex;
    align-items: center;
    padding: 7px 20px;
    background: #00c2b3;
    color: whitesmoke;
  }


  .common-box {
    clear: both;
    margin-top: 37px;

  }

  .box-title {
    font-size: 24px;
    text-align: center;
    position: relative;
    margin-bottom: 24px;
  }

  .box-nav {
    background: #fff;
    display: flex;
    align-items: center;
  }

  .before, .after {
    content: '';
    display: inline-block;
    vertical-align: 8px;
    margin: 0 10px;
    width: 50px;
    height: 1px;
    background: #d1d4db;
  }

  .box-main {
    margin-top: 15px;
    margin-bottom: 20px;
  }

  .job-item {
    /*margin-right: 10px;*/
    margin-bottom: 10px;
    background: #fff;
    display: block;
    height: 130px;
    padding: 16px 20px;
    box-sizing: border-box;
  }

  .info-top {
    display: flex;
    align-items: center;
    width: 100%;
  }

  .name {
    /*float: left;*/
    position: relative;
    max-width: 160px;
    font-size: 15px;
    color: #414a60;
    margin-right: 8px;
    line-height: normal;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .salary {
    /*float: right;*/
    font-size: 15px;
    color: #fd7240;
    line-height: normal;
    flex: 1;
    text-align: right;
    max-width: none;
  }

  .job-text {
    width: 100%;
    /*float: left;*/
    line-height: 28px;
    font-size: 13px;
    color: #8d92a1;
  }

  .item-bottom {
    display: flex;
    align-items: center;
  }


  .item-bottom a {
    display: flex;
    align-items: center;

    height: 30px;
    width: 60px;
    color: #8d92a1;
    font-size: 13px;
  }

  .item-bottom img{
    width: 30px;
    height: 30px;
  }

  .commpany-place {
    height: 100%;
    width: 60%;
    text-align: right;
  }

  .company-item {
    height: 238px;
    margin: 0 0px 16px 0;
    background: #fff;
  }

  .company-info {
    margin: 0 20px 10px;
    padding-top: 30px;
  }

  .company-info a {
    color: #414a60;
  }

  .img-box {
    margin-bottom: 14px;
    text-align: center;
  }

  .conpany-text {
    text-align: center;
  }

  .conpany-text h4 {
    margin-bottom: 10px;
    height: 22px;
    line-height: 22px;
    font-size: 16px;
    font-weight: 400;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .about-info {
    width: 180px;
    height: 34px;
    line-height: 34px;
    font-size: 12px;
    margin: 0 auto;
    text-align: center;
    border: 1px solid #e3e7ed;
  }

  .about-info p {
    color: #61687c;
  }

  .about-info span {
    color: #00c2b3;
  }


</style>



