<script>
  import maintop from "../../../components/maintop";
  import "mavon-editor/dist/css/index.css";

  export default {
    name: "user-main",
    components: {
      'main-top': maintop
    },
    data() {
      return {
        requestHead: {
          'jwt_access_token': ''
        },
        uploadResumeData: {
          'type': '1'
        },
        userDetail: {},
        userDetailTemp: {},                         //用户信息临时变量
        internshipTemp: {
          companyName: '',
          startTime: '',
          endTime: '',
          companyDepartment: '',
          jobName: '',
          isInternship: '1',
          workContent: ''
        },        //实习经历临时变量
        projectExperienceTemp: {
          projectName: '',
          projectRole: '',
          projectStartTime: '',
          projectEndTime: '',
          projectDescription: ''
        },//项目经验临时变量
        educationExperienceTemp: {
          schoolName: '',
          startTime: '',
          endTime: '',
          major: '',
          educationalBackground: '',
          schoolExperience: ''
        }, //项目经验临时变量
        certificateList: [],
        cityList: [],
        jobTypeList: [],
        industryList:[],
        salaryList: [],
        userFile: null,
        summaryDialogVisible: false,
        expectJobDialogVisible: false,
        internshipDialogVisible: false,
        projectExperienceDialogVisible: false,
        educationDialogExperienceVisible: false,
        certificateDialogVisible: false,
        inputVisible: false,
        inputValue: '',
        formLabelWidth: '120px',
        isUpload: false
      }
    },
    methods: {
      edit(type,item){
        this.userDetailTemp = null;
        this.userDetailTemp = JSON.parse(JSON.stringify(this.userDetail));
        const TurndownService = require('turndown').default
        var turndownService = new TurndownService();
        if(type==1){
          this.summaryDialogVisible = true;
          this.userDetailTemp.usermainmessage.advantageMarkdown
            = turndownService.turndown(this.userDetailTemp.usermainmessage.personalAdvantage);
        }else if(type == 2){
          this.expectJobDialogVisible = true;
        }else if(type == 3){
          this.internshipDialogVisible = true;
          this.internshipTemp = JSON.parse(JSON.stringify(item))
          this.internshipTemp.workContentMarkdown
            = turndownService.turndown(this.internshipTemp.workContent);
        }else if (type ==4){
          this.projectExperienceDialogVisible = true;
          this.projectExperienceTemp = JSON.parse(JSON.stringify(item));
          this.projectExperienceTemp.projectDescriptionMarkdown
            = turndownService.turndown(this.projectExperienceTemp.projectDescription);
        }else if(type == 5){
          this.educationDialogExperienceVisible = true;
          this.educationExperienceTemp = JSON.parse(JSON.stringify(item))
          this.educationExperienceTemp.schoolExperienceMarkdown
            = turndownService.turndown(this.educationExperienceTemp.schoolExperience);
        }else if (type == 6){
          this.certificateDialogVisible = true;
        }
      },
      submit(type){
        switch (type) {
          case 1:{
            if (this.userDetailTemp.usermainmessage.workCity instanceof Array) {
              this.userDetailTemp.usermainmessage.workCity = this.userDetailTemp.usermainmessage.workCity[1];
            }
            if (this.userDetailTemp.usermainmessage.expectJobs instanceof Array) {
              this.userDetailTemp.usermainmessage.expectJobs = this.userDetailTemp.usermainmessage.expectJobs[1];
            }
            this.$axios.post(this.APPConstant.API_HEAD+'/re-user-detail/updateUserDetail',this.userDetailTemp.usermainmessage)
              .then(res => {
                if (res.data.code == 0){
                  this.getUserDetailAll();
                  this.summaryDialogVisible = false;
                  this.expectJobDialogVisible = false;
                }
              })
            break;
          }
          case 2:{
            this.$axios.post(this.APPConstant.API_HEAD+'/re-user-internship/updateReUserInternshipExperience',this.internshipTemp)
              .then(res => {
                if (res.data.code == 0){
                  this.getUserDetailAll();
                  this.internshipDialogVisible = false;
                }
              })
            break;
          }
          case 3:{
            this.$axios.post(this.APPConstant.API_HEAD+'/re-user-project-experience/updateReUserInternshipExperience',this.projectExperienceTemp)
              .then(res => {
                if (res.data.code == 0){
                  this.getUserDetailAll();
                  this.projectExperienceDialogVisible = false;
                }
              })
            break;
          }
          case 4:{
            this.$axios.post(this.APPConstant.API_HEAD+'/re-user-education-experience/updateReUserEducationExperience',this.educationExperienceTemp)
              .then(res => {
                if (res.data.code == 0){
                  this.getUserDetailAll();
                  this.educationDialogExperienceVisible = false;
                }
              })
            break;
          }
          case 5:{
            this.$axios.post(this.APPConstant.API_HEAD+'/re-user-certificate/updateTag',this.userDetailTemp.certificateData)
              .then(res => {
                if (res.data.code == 0){
                  this.getUserDetailAll();
                  this.certificateDialogVisible = false;
                }
              })
            break;
          }
          default:{
            return 0;
          }
        }

      },
      deleted(type,item){
        switch (type) {
          case 1:{
            this.$axios.post(this.APPConstant.API_HEAD+'/re-user-internship/deleteReUserInternshipExperience',item)
              .then(res => {
                if (res.data.code == 0){
                  this.getUserDetailAll();
                }
              })
            break;
          }
          case 2:{

            this.$axios.post(this.APPConstant.API_HEAD+'/re-user-project-experience/deleteReUserProjectExperience',item)
              .then(res => {
                if (res.data.code == 0){
                  this.getUserDetailAll();
                }
              })
            break;
          }
          case 3:{
            this.$axios.post(this.APPConstant.API_HEAD+'/re-user-education-experience/deleteReUserEducationExperience',item)
              .then(res => {
                if (res.data.code == 0){
                  this.getUserDetailAll();
                }
              })
            break;
          }
          default:{
            return 0;
          }
        }
      },
      changeDataAdvantage(value, render) {
        this.userDetailTemp.usermainmessage.personalAdvantage = render;
      },
      changeDataInternship(value, render) {
        this.internshipTemp.workContent = render;
      },
      changeDataProject(value, render) {
        this.projectExperienceTemp.projectDescription = render;
      },
      changeDataEducation(value, render) {
        this.educationExperienceTemp.schoolExperience = render;
      },
      addInternship() {
        for (let x in this.internshipTemp) {
          if (x === 'isInternship') { // 如果是null 把直接内容转为 ''
            this.internshipTemp[x] = '1';
          } else {
            this.internshipTemp[x] = ''
          }
        }
        this.internshipDialogVisible = true;
      },
      addProjectExperience() {
        for (let x in this.projectExperienceTemp) {
          this.projectExperienceTemp[x] = ''
        }
        this.projectExperienceDialogVisible = true;
      },
      addEducationExperience() {
        for (let x in this.educationExperienceTemp) {
          this.educationExperienceTemp[x] = ''
        }
        this.educationDialogExperienceVisible = true;
      },
      deleteTag(tag) {
        this.userDetailTemp.certificateData.splice(this.userDetailTemp.certificateData.indexOf(tag), 1);
      },
      showInput() {
        this.inputVisible = true;
        this.$nextTick(_ => {
          this.$refs.saveTagInput.$refs.input.focus();
        });
      },
      addTag() {
        let inputValue = this.inputValue;
        if (inputValue) {
          this.userDetailTemp.certificateData.push(inputValue);
        }
        this.inputVisible = false;
        this.inputValue = '';
      },
      deleteResume(resumeItem) {
        let _this = this;
        this.$axios.post(this.APPConstant.API_HEAD + '/re-user-file/deleteFile', resumeItem)
          .then(res => {
            if (res.data.code == 0) {
              _this.getAllUserFile()
            }
          })
      },
      getUserDetailAll() {
        this.$axios.get(this.APPConstant.API_HEAD + '/re-user-detail/getUserDetailAll')
          .then(res => {
            this.userDetail = res.data.data;
            this.userDetailTemp = JSON.parse(JSON.stringify(this.userDetail));
            localStorage.setItem('userDetail',JSON.stringify(res.data.data.usermainmessage))
          })
      },
      uploading(){
        this.isUpload = true;
      },
      uploadSuccess(){
        this.isUpload = false;
        this.$refs['resume-upload'].clearFiles();
        this.getAllUserFile()
      },
      getAllUserFile() {
        this.$axios.get(this.APPConstant.API_HEAD + '/re-user-file/getAllUserFile')
          .then(res => {
            this.userFile = res.data.data
          })
      },
      getDropData(){
        //城市下拉值
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataCascade?fatherId=30012')
          .then(res => {
            this.cityList = res.data.data
          })
        //职位类型下拉值
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataCascade?fatherId=40001')
          .then(res => {
            this.jobTypeList = res.data.data
          })
        //行业下拉值
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataSimple?fatherId=40003')
          .then(res => {
            this.industryList = res.data.data
          })
        //薪资下拉值
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataSimple?fatherId=40002')
          .then(res => {
            this.salaryList = res.data.data
          })
      },
      initData() {
        //设置请求头
        this.requestHead.jwt_access_token = localStorage.getItem(this.APPConstant.JWT_ACCESS_TOKEN)
        this.getUserDetailAll();
        this.getAllUserFile();
        this.getDropData();
      }
    },
    created() {
      this.initData();
    }
  }
</script>

<style scoped>


  #userDetailEditor {
    background: #f6f6f8;
  }

  a{
    cursor:pointer
  }

  a:hover {
    color: #00c2b3;
  }

  /deep/ .el-upload {
    width: 100%;
  }

  /deep/ .el-upload .el-button {
    width: 100%;
    color: #fff;
    background: #00c2b3;
    /*padding: 17px;*/
  }

  .resume-item .comment-hover:hover {
    background: #f6f6f8;
  }

  .resume-content-box {
    padding: 30px 40px 0;
    background: #fff;
    margin-top: 20px;
  }

  .resume-item {
    position: relative;
    padding: 20px 0;
  }

  .resume-item .op {
    position: absolute;
    display: block;
    right: 3px;
    top: 12px;
  }

  .op a{
    margin-right: 5px;
  }

  .userinfo {
    display: flex;
    align-items: center;
  }

  .head-box {
    width: 56px;
    height: 56px;
  }

  .head-box .head-img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }

  .resume-userinfo .name {
    color: #24272e;
    margin-left: 20px;
    font-size: 24px;
  }


  .info-detail {
    margin-top: 15px;
  }


  .resume-edit-advantage {
    position: relative;
  }

  .resume-item .title {
    margin-bottom: 20px;
    position: relative;
  }

  .resume-item .title a {
    position: absolute;
    right: 5px;
    font-size: 14px;
    font-weight: 400;
    color: #00c2b3;
  }

  .resume-item .title a:hover {
    color: #00d7c6;
  }

  .resume-edit-advantage .advantage-text {
    padding: 8px 0 8px 20px;
    width: 80%;
  }

  .expect-job .expect-message {
    padding: 8px 0 8px 20px;
    position: relative;
    min-height: 20px;
  }

  .expect-job .expect-message .op {
    top: 8px;
  }

  .comment-item {
    padding: 8px 0 8px 20px;
    position: relative;
    min-height: 40px;
  }

  .comment-item .info-text {
    padding: 10px 0;
  }

  .comment-item h4 {
    font-size: 15px;
    font-weight: 700;
  }

  .comment-item .name {
    max-width: 420px;
    word-break: break-all;
    float: left;
    color: #414a60;
  }

  .comment-item .period {
    font-size: 12px;
    padding-left: 30px;
    float: left;
    position: relative;
    top: 2px;
  }

  .comment-item .internship-tag {
    width: 44px;
    height: 20px;
    line-height: 20px;
    border-radius: 10px;
    border: 1px solid #3a98fc;
    margin-left: 20px;
    font-size: 12px;
    font-weight: 500;
    color: #3a98fc;
    display: inline-block;
    text-align: center;
  }

  .comment-item .text-content {
    font-weight: 700;
  }

  .comment-item .info-text .text-box {
    position: relative;
    left: 60px;
    top: -18px;
    display: block;
    width: 90%;
  }

  .education-experience .education-text{
    position: relative;
    left: 80px;
    top: -18px;
    display: block;
    width: 90%;

  }

  .comment-item .ability-tag {
    padding: 7px 0 5px;
  }

  .comment-item .ability-tag span {
    display: inline-block;
    font-size: 12px;
    line-height: 20px;
    color: #9fa3b0;
    padding: 0 14px;
    margin-right: 10px;
    border: 1px solid #cfd1d7;
    border-radius: 50px;
  }


  .aside-box {
    background: #fff;
    margin-top: 20px;
    padding: 20px 30px;
    margin-bottom: 16px;
  }

  .file-main {
    margin-top: 10px;
  }

  .file-item {
    position: relative;
    color: #414a60;
    font-size: 14px;
    cursor: default;
    margin-top: 10px;
  }

  .file-item .el-icon-delete {
    float: right;
  }

  .file-btns {
    /*height: 30px;*/
    margin-top: 10px;
    text-align: center;
  }

  .el-tag + .el-tag {
    margin-left: 10px;
  }

  .button-new-tag {
    margin-left: 10px;
    height: 32px;
    line-height: 30px;
    padding-top: 0;
    padding-bottom: 0;
  }

  .input-new-tag {
    width: 90px;
    margin-left: 10px;
    vertical-align: bottom;
  }
</style>



