<script>
  import maintop from "../../../components/maintop";

  export default {
    name: "companyDetail",
    components: {
      'main-top': maintop
    },
    data() {
      return {
        isLogin: false,
        imgList: [
          {
            url: require("@/assets/img.jpg")
          },
          {
            url: require("@/assets/img1.jpg")
          },
          {
            url: require("@/assets/img2.jpg")
          },
          {
            url: require("@/assets/img3.jpg")
          }
        ],
        watchJobSList: [
          {
            jobTitle: '阿里巴巴校招java工程师',
            minSalary: '16k',
            maxSalary: '25k',
            companyName: '阿里巴巴集团'
          },
          {
            jobTitle: '阿里巴巴校招java工程师',
            minSalary: '16k',
            maxSalary: '25k',
            companyName: '阿里巴巴集团'
          },
          {
            jobTitle: '阿里巴巴校招java工程师',
            minSalary: '16k',
            maxSalary: '25k',
            companyName: '阿里巴巴集团'
          }
        ],
        jobCategoryList: [],
        companyDetail: {},
        jobList: {},
        uploadResumeData: {
          'type': '1'
        },
        requestHead: {
          'jwt_access_token': ''
        },
        dialogVisible: false,
        searchConditions:{
          companyId: null,
          jobCategory: null,
          current: 1,
          size: 10
        },
        showIndex: '1',
        categoryIndex: -1
      }
    },
    methods: {
      changeCategory(index, jobCategory) {
        this.categoryIndex = index
        this.searchConditions.jobCategory = jobCategory
        this.getCompanyJobs(1);
      },
      changePage(key, keyPath) {
        if (key == '1') {
          this.getCompanyMessage();
        } else {
          this.searchConditions.jobCategory = null
          this.getCompanyJobs(1);
          this.getCompanyJobCategory();
        }
        this.showIndex = key;
      },
      //查询公司详细信息
      getCompanyMessage() {
        this.$axios.get(this.APPConstant.API_HEAD + '/re-company/getCompanyMessageDetail?companyId=' + this.searchConditions.companyId)
          .then(res => {
            this.companyDetail = res.data.data
          })
      },
      //查询公司职位分类列表
      getCompanyJobCategory() {
        this.$axios.get(this.APPConstant.API_HEAD + '/re-company/getCompanyJobCategory?companyId=' + this.searchConditions.companyId)
          .then(res => {
            this.jobCategoryList = res.data.data
          })
      },
      //查询公司对应职位
      getCompanyJobs(pageNum) {
        this.searchConditions.current = pageNum;
        this.$axios.post(this.APPConstant.API_HEAD + '/re-company/searchCompanyJobs',this.searchConditions)
          .then(res => {
            this.jobList = res.data.data
          })
      },
      //上传成功钩子
      uploadSuccess() {
        this.dialogVisible = false;
      },
      toJobDetail(jobId) {
        this.$router.push('/jobDetail?jobId=' + jobId)
      },
      initData() {
        this.searchConditions.companyId = this.$route.query.companyId;
        //设置请求头
        if(localStorage.getItem('userDetail')!=null){
          this.requestHead.jwt_access_token = localStorage.getItem(this.APPConstant.JWT_ACCESS_TOKEN)
          this.isLogin = true;
        }else {
          this.isLogin = false;
        }
        this.getCompanyMessage();
      }
    },
    created() {
      this.initData();
    }
  }
</script>

