import Vue from 'vue'
import axios from "axios";
import global from "../global";
import Router from 'vue-router'
import login from "../view/login/login";
import qSlideVerify from "../components/qSlideVerify";
import mainPage from "../view/recruitment/main/mainPage";
import userCenter from "../view/recruitment/user/userCenter";
import userDetailEditor from "../view/recruitment/user/userDetailEditor";
import jobMain from "../view/recruitment/main/jobMain";
import companyMain from "../view/recruitment/main/companyMain";
import jobDetail from "../view/recruitment/detail/jobDetail";
import companyDetail from "../view/recruitment/detail/companyDetail";
import userCollection from "../view/recruitment/user/userCollection";
import bossLogin from "../view/login/bossLogin";
import bossMainPage from "../view/company/bossMainPage";
import editCompanyPage from "../view/company/editCompanyPage";
import editBusinessPage from "../view/company/editBusinessPage";
import editJobPage from "../view/company/editJobPage";
import applyPage from "../view/company/applyPage";
import applyDetail from "../view/company/applyDetail";
import hirePage from "../view/company/hirePage";

Vue.use(Router)


const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/login',
      name: 'login',
      component: login,
      meta: {
        title: '慧眼网页版登录'
      }
    },
    {
      path: '/qSlideVerify',
      name: 'qSlideVerify',
      component: qSlideVerify
    },
    {
      path: '/',
      name: 'mainPage',
      component: mainPage,
      meta: {
        title: '慧眼 海量人才招聘'
      }
    },
    {
      path: '/user/center',
      name: 'userCenter',
      component: userCenter,
      meta: {
        title: '慧眼'
      }
    },
    {
      path: '/user/resume',
      name: 'resume',
      component: userDetailEditor,
      meta: {
        title: '慧眼'
      }
    },
    {
      path: '/jobMain',
      name: 'jobMain',
      component: jobMain,
      meta: {
        title: '慧眼'
      }
    },
    {
      path: '/companyMain',
      name: 'companyMain',
      component: companyMain,
      meta: {
        title: '慧眼'
      }
    },
    {
      path: '/jobDetail',
      name: jobDetail,
      component: jobDetail,
      meta:{
        title: '慧眼'
      }
    },
    {
      path: '/companyDetail',
      name: companyDetail,
      component: companyDetail,
      meta:{
        title: '慧眼'
      }
    },
    {
      path: '/user/userCollection',
      name: userCollection,
      component: userCollection,
      meta:{
        title: '慧眼'
      }
    },
    {
      path: '/boss/login',
      name: bossLogin,
      component: bossLogin,
      meta:{
        title: '慧眼直聘企业端-登录'
      }
    }
    ,
    {
      path: '/boss/bossMainPage',
      name: bossMainPage,
      component: bossMainPage,
      meta:{
        title: '慧眼直聘企业端'
      }
    }
    ,
    {
      path: '/boss/editCompanyPage',
      name: editCompanyPage,
      component: editCompanyPage,
      meta:{
        title: '慧眼直聘企业端'
      }
    }
    ,
    {
      path: '/boss/editBusinessPage',
      name: editBusinessPage,
      component: editBusinessPage,
      meta:{
        title: '慧眼直聘企业端'
      }
    }
    ,
    {
      path: '/boss/editJobPage',
      name: editJobPage,
      component: editJobPage,
      meta:{
        title: '慧眼直聘企业端'
      }
    }
    ,
    {
      path: '/boss/applyPage',
      name: applyPage,
      component: applyPage,
      meta:{
        title: '慧眼直聘企业端'
      }
    }
    ,
    {
      path: '/boss/applyDetail',
      name: applyDetail,
      component: applyDetail,
      meta:{
        title: '慧眼直聘企业端-求职者简历'
      }
    }
    ,
    {
      path: '/boss/hirePage',
      name: hirePage,
      component: hirePage,
      meta:{
        title: '慧眼直聘企业端'
      }
    }
  ]
})

// 导航守卫
// 使用 router.beforeEach 注册一个全局前置守卫，判断用户是否登陆
router.beforeEach((to, from, next) => {

  //改变页面标题
  document.title = to.meta.title
  //判断是否登录，使用token校验
  if(to.path === '/boss/login'){
    axios({
      methods: 'get',
      url: global.API_HEAD + '/re-company-boss/isLogin',
    }).then((res) => {
        if (res.data.code === 0) {
          next('/boss/bossMainPage');
        } else {
          localStorage.removeItem(global.ACCESS_BOSS_TOKEN)
          localStorage.removeItem('bossDetail')
          next();
        }
      }
    ).catch((error) => {
      localStorage.removeItem(global.ACCESS_BOSS_TOKEN)
      localStorage.removeItem('bossDetail')
      next();
    })
  }
  else if ((to.path).indexOf('/boss/') === 0) {
    axios({
      methods: 'get',
      url: global.API_HEAD + '/re-company-boss/isLogin',
    }).then((res) => {
        if (res.data.code === 0) {
          next();
        } else {
          localStorage.removeItem(global.ACCESS_BOSS_TOKEN)
          localStorage.removeItem('bossDetail')
          next('/boss/login');
        }
      }
    ).catch((error) => {
      localStorage.removeItem(global.ACCESS_BOSS_TOKEN)
      localStorage.removeItem('bossDetail')
      next('/boss/login');
    })
  }
  else {
    axios({
      methods: 'get',
      url: global.API_HEAD + '/re-user/isLogin',
    }).then((res) => {
        if (res.data.code != 0) {
          localStorage.removeItem(global.JWT_ACCESS_TOKEN)
          localStorage.removeItem('userDetail')
        }else {
          localStorage.setItem('userDetail', JSON.stringify(res.data.data))
        }
        next();
      }
    ).catch((error) => {
      next();
    })
  }
/*  if (to.path === '/login') {
    axios({
      methods: 'post',
      url: global.API_HEAD + '/re-user/isLogin',
    }).then((res) => {
        if (res.data.code === 0) {
          next('/');
        } else {
          localStorage.removeItem(global.JWT_ACCESS_TOKEN)
          next();
        }
      }
    ).catch((error) => {
      next();
    })
  }*/ /*else if(to.path === '/boss/login'){
    axios({
      methods: 'post',
      url: global.API_HEAD + '/re-user/isLogin',
    }).then((res) => {
        if (res.data.code === 0) {
          next('/');
        } else {
          localStorage.removeItem(global.JWT_ACCESS_TOKEN)
          next();
        }
      }
    ).catch((error) => {
      next();
    })
  }*/
/*  else {
    axios({
      methods: 'post',
      url: global.API_HEAD + '/re-user/isLogin',
    }).then((res) => {
        if (res.data.code === 0) {
          next();
        } else {
          localStorage.removeItem(global.JWT_ACCESS_TOKEN)
          localStorage.removeItem('userDetail')
          next('/login');
        }
      }
    ).catch((error) => {
      next('/login');
    })
  }*/
});

export default router;




