
import Vue from 'vue'
import Vuex from 'vuex'
import axios from "axios";              //引入axios
import App from './App'
import router from './router'
import ElementUI from 'element-ui'      //引入element-ui
import SlideVerify from './lib/index'
import global from "./global";          //引入全局变量
import mavonEditor from 'mavon-editor'  //引入markdown编辑器
import 'mavon-editor/dist/css/index.css'
import 'element-ui/lib/theme-chalk/index.css'

Vue.use(ElementUI)
Vue.use(SlideVerify)
Vue.use(Vuex);
Vue.use(mavonEditor)
Vue.config.productionTip = false
Vue.prototype.$axios = axios
Vue.prototype.APPConstant = global


// 添加请求拦截器，在请求头中加token(招聘者和boss的都带上)
axios.interceptors.request.use(
  config => {
    if (localStorage.getItem('jwt_access_token')) {
      config.headers.jwt_access_token = localStorage.getItem('jwt_access_token');
    }
    if (localStorage.getItem('boss_token')){
      config.headers.boss_token = localStorage.getItem('boss_token');
    }

    return config;
  },
  error => {
    return Promise.reject(error);
  });



new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



