<template>
  <el-container>
    <el-header>
      <company-top></company-top>
    </el-header>
    <el-container>
      <el-aside width="200px" style="background: whitesmoke;" class="aside">
        <company-aside active="5"></company-aside>
      </el-aside>
      <el-main >
        <div class="main-box">
          <el-form  :inline="true" class="from-box">
            <el-form-item label="职位名称" >
              <el-input v-model="conditions.jobTitle" placeholder="职位名称"></el-input>
            </el-form-item>

            <el-form-item label="投递日期" >
              <el-date-picker
                v-model="conditions.applyDate"
                value-format="yyyy-MM-dd"
                align="right"
                type="date"
                placeholder="选择日期"
                :picker-options="pickerOptions">
              </el-date-picker>
            </el-form-item>
            <el-form-item >
              <el-button type="primary" @click="searchCompanyDeliverer" icon="el-icon-search" size="mini">
                搜索
              </el-button>
              <el-button icon="el-icon-refresh-right" @click="clearConditions" size="mini">
                重置
              </el-button>
            </el-form-item>
          </el-form>
          <el-table
            v-if="delivererData!=null"
            class="table-box"
            ref="multipleTable"
            :data ="delivererData"
            tooltip-effect="dark"
            :default-sort="{prop: 'date', order: 'descending'}"
            height="500"
            max-height="500"
          >

            <el-table-column
              label="职位标题"
              prop="jobTitle"
              width="200"
            >
            </el-table-column>
            <el-table-column
              prop="name"
              label="投递人"
            >
            </el-table-column>
            <el-table-column
              prop="highestEducationBackground"
              label="学历"
            >
            </el-table-column>
            <el-table-column
              prop="userTypeApplication"
              label="投递人类型"
            >
            </el-table-column>
            <el-table-column
              prop="workCity"
              label="所在城市"
              >
            </el-table-column>
            <el-table-column
              prop="telephoneNumber"
              label="电话号码"
              >
            </el-table-column>
            <el-table-column
              prop="statusApplication"
              label="状态"
            >
            </el-table-column>
            <el-table-column
              prop="applyDate"
              label="投递日期"
            >
            </el-table-column>
            <el-table-column
              label="操作"
            >
              <template slot-scope="scope">
                <el-button
                  @click="toApplyDetail(scope.$index,scope.row)"
                  type="text"
                  size="small">
                  查看详情
                </el-button>
                <el-button
                  type="text"
                  size="small"
                  @click="invitedInterview(scope.row)">
                  {{scope.row.btnText}}
                </el-button>
              </template>
            </el-table-column>
          </el-table>
          <el-dialog :title="'填写应聘者'+ feedbackTemp.name+'的应聘结果'"
                     :visible.sync="editDialogVisible">
            <el-form>
              <el-form-item label="应聘职位">
                <el-input :disabled="true" v-model="feedbackTemp.jobTitle"></el-input>
              </el-form-item>
              <el-form-item label="是否通过面试">
                <el-radio-group v-model="feedbackTemp.result">
                  <el-radio :disabled="dialogDisabledEdit"  label="1">是</el-radio>
                  <el-radio :disabled="dialogDisabledEdit" label="0">否</el-radio>
                </el-radio-group>
              </el-form-item>
              <el-form-item label="编辑回复信息">
                <mavon-editor
                  defaultOpen = 'edit'
                  v-model="feedbackTemp.descriptionMarkdown"
                  :toolbars="this.APPConstant.toolbars"
                  @keydown=""
                  @change="changeDescription"
                />
              </el-form-item>
            </el-form>

            <div slot="footer" class="dialog-footer">
              <el-button @click="editDialogVisible = false">退出</el-button>
              <el-button v-if="!dialogDisabledEdit" type="primary" @click="commitFeedBack">提 交</el-button>
            </div>
          </el-dialog>

        </div>
      </el-main>
    </el-container>

  </el-container>
</template>

<script>
  import companyTop from "../../components/companyTop";
  import companyAside from "../../components/companyAside";

  export default {
    name: "applyPage",
    components: {
      "company-top": companyTop,
      "company-aside": companyAside
    },
    data(){
      return{
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          },
        },
        dialogDisabledEdit: false,
        feedbackTemp:{
          name: '',
          jobTitle: '',
          applyRecordId: '',
          result: '',
          description: '',
          descriptionMarkdown: '',
        },
        editDialogVisible: false,
        conditions:{
          jobTitle: null,
          applyDate: null
        },
        tableData: [
          {
            'address':'广州'
          },
          {
            'address':'广州'
          },
          {
            'address':'广州'
          },
          {
            'address':'广州'
          },
          {
            'address':'广州'
          }

        ],
        delivererData: null
      }
    },
    methods:{
      commitFeedBack(){
        this.feedbackTemp.type = '2'
        this.$axios.post(this.APPConstant.API_HEAD+'/re-company-boss/changeDelivererStatus',this.feedbackTemp)
          .then(res => {
            if (res.data.code == 0 ){
              this.$alert('成功反馈给求职者！', '提示', {
                confirmButtonText: '确定',
                callback: action => {
                  this.searchCompanyDeliverer()
                }
              });
            }
          })
      },
      changeDescription(value, render) {
        this.feedbackTemp.description = render;
      },
      clearConditions(){
        for (let conditionsKey in this.conditions) {
          this.conditions[conditionsKey] = ''
        }
        this.searchCompanyDeliverer()
      },
      toApplyDetail(index,row){
        let routeUrl = this.$router.resolve('/boss/applyDetail?userId='+row.userId+'&jobId='+row.jobId);
        window.open(routeUrl.href, '_blank');
      },
      invitedInterview(row){
        if(row.status == '1'){
          var params = {
            applyRecordId: row.applyRecordId,
            type: row.status
          }
          this.$axios.post(this.APPConstant.API_HEAD+'/re-company-boss/changeDelivererStatus',params)
            .then(res => {
              if (res.data.code == 0 ){
                this.$alert('成功邀约应聘者面试！', '提示', {
                  confirmButtonText: '确定',
                  callback: action => {
                    this.searchCompanyDeliverer()
                  }
                });
              }
            })
        }
        else if(row.status == '2'){
          for (let key in this.feedbackTemp) {
            this.feedbackTemp[key] = ''
          }
          this.feedbackTemp.name = row.name
          this.feedbackTemp.jobTitle = row.jobTitle
          this.feedbackTemp.applyRecordId = row.applyRecordId
          this.editDialogVisible = true
        }
        else {
          this.$axios.get(this.APPConstant.API_HEAD+'/re-company-boss/getFeedbackDatail?applyRecordId='+row.applyRecordId)
            .then(res =>{
              if(res.data.code == 0){
                for (let key in this.feedbackTemp) {
                  this.feedbackTemp[key] = ''
                }
                this.feedbackTemp.name = row.name
                this.feedbackTemp.jobTitle = row.jobTitle
                this.feedbackTemp.description = res.data.data.description;
                this.feedbackTemp.result = res.data.data.result
                const TurndownService = require('turndown').default
                var turndownService = new TurndownService();
                this.feedbackTemp.descriptionMarkdown
                  = turndownService.turndown(res.data.data.description);
                this.dialogDisabledEdit = true
                this.editDialogVisible = true
              }
            })
        }
      },
      searchCompanyDeliverer(){
        this.$axios.post(this.APPConstant.API_HEAD+'/re-company-boss/searchCompanyDeliverer',this.conditions)
        .then(res => {
          if(res.data.code === 0){
            this.delivererData = res.data.data;
          }
        })
      },
      initData(){
        this.searchCompanyDeliverer()
      }
    },
    created() {
      this.initData();
    }
  }
</script>

<style scoped>
  .el-container {
    width: 100%;
    height: 700px;
  }

  .el-header {
    padding: 0;
  }

  .el-aside {
    height: 100%;
  }

  .main-box {
    width: 90%;
    margin-left: 50px;
  }

  .from-box {
    display: flex;
    align-items: center;
  }
</style>



