<template>
  <el-container>
    <el-header>
      <company-top></company-top>
    </el-header>
    <el-container>
      <el-aside width="200px" style="background: whitesmoke;" class="aside">
        <company-aside active="3"></company-aside>
      </el-aside>
      <el-main >
        <div class="main-box">
          <el-form  :inline="true" :model="businessInformation" class="form-item">
            <el-form-item label="工商名">
              <el-input  placeholder="工商名" v-model="businessInformation.businessName"></el-input>
            </el-form-item>
            <el-form-item label="法定代表人">
              <el-input  placeholder="法定代表人" v-model="businessInformation.representative"></el-input>
            </el-form-item>
            <el-form-item label="注册资本">
              <el-input  placeholder="注册资本" v-model="businessInformation.registeredCapital"></el-input>
            </el-form-item>
            <el-form-item label="成立日期">
              <el-input  placeholder="成立日期" v-model="businessInformation.setUpDate"></el-input>
            </el-form-item>
            <el-form-item label="企业类型">
              <el-input  placeholder="企业类型" v-model="businessInformation.enterpriseType"></el-input>
            </el-form-item>
            <el-form-item label="经营状态">
              <el-input  placeholder="经营状态" v-model="businessInformation.operatingState"> </el-input>
            </el-form-item>
            <el-form-item label="注册地址">
              <el-input  placeholder="注册地址" v-model="businessInformation.registeredAddress"></el-input>
            </el-form-item>
            <el-form-item label="统一社会信用代码">
              <el-input  placeholder="统一社会信用代码" v-model="businessInformation.code"></el-input>
            </el-form-item>

          </el-form>
          <div class="text-box">
            <el-form>
              <el-form-item label="经营范围">
                <mavon-editor
                  defaultOpen = 'edit'
                  v-model="scopeBusinessMarkDown"
                  @change="changeScopeBusiness"
                  :toolbars="this.APPConstant.toolbars"
                  @keydown=""
                />
              </el-form-item>
            </el-form>
          </div>
          <div class="submit">
            <el-button  type="primary" size="medium" @click="commitUpdate">提交信息</el-button>
            <el-button   size="medium" @click="getBusinessInformation">重置信息</el-button>

          </div>
        </div>

      </el-main>
    </el-container>

  </el-container>
</template>

<script>
  import companyTop from "../../components/companyTop";
  import companyAside from "../../components/companyAside";

  export default {
    name: "bossMainPage",
    components: {
      "company-top": companyTop,
      "company-aside": companyAside
    },
    data(){
      return{
        businessInformation: {
          "businessName":"",
          "code":"",
          "companyId":"",
          "createTime":"",
          "enterpriseType":"",
          "informationId":"",
          "operatingState":"",
          "registeredAddress":"",
          "registeredCapital":"",
          "representative":"",
          "scopeBusiness":'',
          "setUpDate": ''
        },
        scopeBusinessMarkDown: null
      }
    },
    methods:{
      changeScopeBusiness(value, render) {
        this.businessInformation.scopeBusiness = render;
      },
      commitUpdate(){
        this.$axios.post(this.APPConstant.API_HEAD+'/re-company-boss/updateBusinessDetail',this.businessInformation)
          .then(res =>{
            if (res.data.code == 0){
              this.$alert('修改成功！', '提示', {
                confirmButtonText: '确定',
                callback: action => {
                  this.getBusinessInformation()
                }
              });
            }
          })
      },
      getBusinessInformation(){
        this.$axios.get(this.APPConstant.API_HEAD+'/re-company-boss/getBusinessDetail')
        .then(res =>{
          if (res.data.data!=null){
            this.businessInformation = res.data.data;
            if(this.businessInformation.scopeBusiness !=null){
              const TurndownService = require('turndown').default
              var turndownService = new TurndownService();
              this.scopeBusinessMarkDown
                = turndownService.turndown(this.businessInformation.scopeBusiness);
            }
          }
        })
      },
      initData(){
        this.getBusinessInformation();
      }
    },
    created() {
      this.initData()
    }
  }
</script>

<style scoped>
  .el-form--inline .el-form-item {

    margin-right: 100px;

  }
  .el-container {
    width: 100%;
    height: 700px;
  }

  .el-header {
    padding: 0;
  }

  .el-aside {
    height: 100%;
  }

  .main-box{
    width: 90%;
    margin-left: 50px;
  }

  .main-box .submit{
    width: 19%;
    margin: 0 auto;
  }

</style>



