<template>
  <el-menu :default-active="active" @select="changePage">
    <el-menu-item index="1">
      <i class="el-icon-menu"></i>
      <span slot="title">招聘概况</span>
    </el-menu-item>
    <el-menu-item index="2">
      <i class="el-icon-menu"></i>
      <span slot="title">公司信息</span>
    </el-menu-item>
    <el-menu-item index="3">
      <i class="el-icon-menu"></i>
      <span slot="title">工商信息</span>
    </el-menu-item>
    <el-menu-item index="4">
      <i class="el-icon-menu"></i>
      <span slot="title">职位列表</span>
    </el-menu-item>
    <el-menu-item index="5">
      <i class="el-icon-menu"></i>
      <span slot="title">应聘信息</span>
    </el-menu-item>
    <el-menu-item index="6">
      <i class="el-icon-menu"></i>
      <span slot="title">我要招人</span>
    </el-menu-item>
  </el-menu>

</template>

<script>
  export default {
    name: "companyAside",
    props: ["active"],
    data() {
      return {

      }
    },
    methods: {
      changePage(key, keyPath) {
        if(key==='1'){
          this.$router.push('/boss/bossMainPage')
        }else if(key === '2'){
          this.$router.push('/boss/editCompanyPage')
        } else if(key === '3'){
          this.$router.push('/boss/editBusinessPage')
        }else if (key === '4'){
          this.$router.push('/boss/editJobPage')
        }else if (key === '5'){
          this.$router.push('/boss/applyPage')
        }else {
          this.$router.push('/boss/hirePage')
        }
      }
    }
  }
</script>

<style scoped>

</style>



