<template>
  <div id="companyMain">
    <main-top selected="/companyMain" ></main-top>

    <el-row class="filter-condition">
      <el-col :span="18" :offset="3">
        <el-row class="filter-row">
          <span class="title">公司地点：</span>
          <span v-for="(city,index) in 9" :key="index" :class="cityIndex===index?'selected item':'item'"
                @click="changeCondition(index,city,'city')">
              全国
          </span>
        </el-row>
        <el-row class="filter-row">
          <span class="title">行业类型：</span>
          <el-col :span="21">
            <span :class="industryIndex===-1?'selected item':'item'" @click="changeCondition(-1,null,'industry')">不限</span>
            <span v-for="(industryItem,index) in industryList" :key="index" :class="industryIndex===index?'selected item':'item'"
                  @click="changeCondition(index,industryItem.dmName,'industry')">
              {{industryItem.dmName}}
          </span>
          </el-col>

        </el-row>
        <el-row class="filter-row">
          <span class="title">融资阶段：</span>
          <span :class="financingStageIndex===-1?'selected item':'item'" @click="changeCondition(-1,null,'financingStage')">不限</span>
          <span v-for="(financingItem,index) in financingList" :key="index" :class="financingStageIndex===index?'selected item':'item'"
                @click="changeCondition(index,financingItem.dmName,'financingStage')">
              {{financingItem.dmName}}
          </span>
        </el-row>
        <el-row class="filter-row">
          <span class="title">公司规模：</span>
          <span :class="companySizeIndex===-1?'selected item':'item'" @click="changeCondition(-1,null,'companySize')">不限</span>
          <span v-for="(sizeItem,index) in sizeList" :key="index" :class="companySizeIndex===index?'selected item':'item'"
                @click="changeCondition(index,sizeItem.dmName,'companySize')">
              {{sizeItem.dmName}}
          </span>
        </el-row>
      </el-col>
    </el-row>
    <el-row v-if="userDetail!=null" class="expect">
      <el-col :span="18" :offset="3">
        <el-checkbox
          v-model="isExpect"
          text-color="#fff"
          @change="changeUserConditions"
        >
          根据求职期望 &#12288; {{userDetail.workCity}} &#12288;|&#12288;{{userDetail.expectJobs}} &#12288;推荐
        </el-checkbox>
      </el-col>
    </el-row>
    <el-row class="main">
      <el-col :span="18" :offset="3">
        <el-row :gutter="20" class="main-inner">
          <el-col v-for="(companyItem,index) in companyMainList.list" :span="6" :key="index">
            <div class="company-main">
              <div class="item-main">
                <a :href="'/companyDetail?companyId='+companyItem.companyId" target="_blank">
                  <img :src="companyItem.imgUrl"/>
                  <div class="copany-text">
                    <h3>{{companyItem.companyName}}</h3>
                    <p>{{companyItem.financingStage}} &#12288;|&#12288; {{companyItem.industry}}</p>
                  </div>
                </a>
                <br clear="all"/>
              </div>

                            <div class="my-line" style="background: #DCDFE6;width: 230px;height: 1px;margin-top: 12px;margin-bottom: 12px"></div>
            </div>

          </el-col>
        </el-row>
        <el-row class="page-box">
          <el-col :offset="8">
            <el-pagination
              :page-size="companyMainList.pageSize"
              :current-page="companyMainList.pageNum"
              layout="prev, pager, next"
              @current-change="searchCompanyMain"
              :total="companyMainList.total">
            </el-pagination>
          </el-col>
        </el-row>

      </el-col>
    </el-row>


  </div>
</template>

<script>
  import mainTob from "../../../components/maintop";

  export default {
    name: "companyMain",
    components: {
      "main-top": mainTob
    },
    data() {
      return {
        userDetail: null,
        cityIndex: 0,
        industryIndex: -1,
        financingStageIndex: -1,
        companySizeIndex: -1,
        searchConditions: {
          city: '',//所在城市
          industry: '',//行业类别
          financingStage: '',//融资阶段
          companySize: '' ,//公司规模
          current: 1, //当前页
          size: 10//每页数据量
        },
        industryList: [],//公司行业数据
        financingList: [],//融资数据
        sizeList: [], //公司规模数据
        companyMainList: [], //公司信息列表
        isExpect: false
      }
    },
    methods: {
      //是否启用用户条件搜索
      changeUserConditions(){
        if (this.isExpect && this.userDetail!=null){
          this.searchConditions.city = this.userDetail.workCity;
          this.searchConditions.industry = this.userDetail.expectIndustry;
        }else {
          this.searchConditions.city = null
          this.searchConditions.industry = null
        }
        this.industryIndex = -1;
        this.financingStageIndex = -1;
        this.companySizeIndex = -1;
        this.searchConditions.financingStage = null
        this.searchConditions.companySize = null
        this.searchCompanyMain(1)
      },
      //改变搜索条件的方法
      changeCondition(index, value, type) {
        if (type === 'city') {
          this.cityIndex = index;
          // this.searchConditions.city = value
        } else if (type === 'industry') {
          this.industryIndex = index;
          this.searchConditions.industry = value;
        } else if (type === 'financingStage') {
          this.financingStageIndex = index;
          this.searchConditions.financingStage = value
        } else if (type === 'companySize') {
          this.companySizeIndex = index;
          this.searchConditions.companySize = value
        } else {
          return 0;
        }
        this.searchCompanyMain(1)
      },
      //搜索公司方法
      searchCompanyMain(current){
        this.searchConditions.current = current;
        this.$axios.post(this.APPConstant.API_HEAD + '/re-company/searchCompany',this.searchConditions)
          .then(res => {
            this.companyMainList = res.data.data;
          })
      },
      //初始化方法
      initData(){
        this.userDetail = JSON.parse(localStorage.getItem('userDetail'))
        this.changeUserConditions();
        //行业下拉值
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataSimple?fatherId=40003')
          .then(res => {
            this.industryList = res.data.data
          })
        //融资阶段下拉
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataSimple?fatherId=40007')
          .then(res => {
            this.financingList = res.data.data
          })
        //公司规模下拉
        this.$axios.get(this.APPConstant.API_HEAD + '/gy-base-dmcode/getDataSimple?fatherId=40008')
          .then(res => {
            this.sizeList = res.data.data
          })
        //获取公司数据
        this.searchCompanyMain(1);
      }
    },
    created() {
      this.initData();
    }
  }
</script>

<style scoped>

  #companyMain {
    background: #f6f6f8;
  }

  .filter-condition {
    background: #fff;
    padding: 30px 0;
  }

  .filter-row {
    margin-bottom: 13px;
    display: flex;
    /*align-items: center;*/
  }

  .filter-row .title {
    width: 70px;
    font-size: 14px;
    font-weight: 500;
    color: #414a60;
  }

  .filter-row .item {
    margin-right: 6px;
    padding: 2px 8px;
    cursor: pointer;
    font-size: 13px;
    float: left;
    display: inline-block;
  }

  .filter-row .item:hover {
    color: #00c2b3;
  }

  .filter-row .selected {
    color: #00c2b3;
  }

  .expect {
    margin: 27px 0;
  }

  .expect span {
    color: #00c2b3;
  }

  .company-main {
    /*margin-right: 10px;*/
    margin-bottom: 10px;
    background: #fff;
    display: block;
    height: 130px;
    padding: 16px 20px;
    box-sizing: border-box;
  }


  .item-main {
    padding: 10px 0;

  }


  .item-main img {
    float: left;
    width: 57px;
    height: 57px;
  }


  .item-main h3 {
    float: right;
    height: 25px;
    line-height: 25px;
    font-size: 16px;
    font-weight: 400;
    max-width: 130px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    -o-text-overflow: ellipsis;
    color: black;
  }

  .item-main p {
    float: right;
    height: 40px;
    line-height: 41px;
    color: #898e9d;
    font-size: 12px;
    width: 150px;
    text-align: right;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }


  .page-box {
    background: #fff;
    padding: 10px;
  }

</style>



