<template>
  <div id="app-login">
    <div class="background">
      <img :src="imgSrc" width="100%" height="110%" style="margin-top: -15px" alt=""/>
    </div>


    <el-row>
      <el-col :span="10" :offset="7">
        <div class="login-box">
          <div class="form-aside">
            <a class="logo" href="http://www.baidu.com">
              <img class="logo-image" src="https://static.zhipin.com/v2/web/geek/images/logo.png">
              <div class="logo-text">
                <p>找牛人</p>
                <p>就用慧眼直聘</p>
              </div>
            </a>
            <a class="aside-other">
              <img class="logo-image" src="../../assets/loginImage/loginIcon1.jpg">
              <div class="other-text">
                <p>沟通</p>
                <p>与牛人及时沟通</p>
              </div>
            </a>
            <a class="aside-other">
              <img class="logo-image" src="../../assets/loginImage/loginIcon2.jpg">
              <div class="other-text">
                <p>随意选</p>
                <p>海量牛人双向选择</p>
              </div>
            </a>

          </div>

          <div class="form-main">
            <el-form ref="loginForm" :model="form" :rules="rules" v-if="loginRegistReplace" label-width="80px"
                     class="main-box">


              <el-form-item label="账号" prop="username">
                <el-input type="text" placeholder="请输入账号" v-model="form.bossAccount"/>
              </el-form-item>
              <el-form-item label="密码" prop="password">
                <el-input type="password" placeholder="请输入密码" v-model="form.password"/>
              </el-form-item>
              <el-form-item prop="isverify" style="width: 115%;margin-left: -15%">
                <el-popover
                  placement="right"
                  width="400"
                  heigth="600"
                  trigger="click">
                  <q-slide-verify v-if="!form.isverify" v-model="form.isverify"></q-slide-verify>
                  <el-button type="success" slot="reference" style="width: 100%" :disabled="form.isverify">
                    {{ form.isverify==false?'点击按钮进行验证':'验证成功' }}
                  </el-button>
                </el-popover>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" v-on:click="onSubmit('loginForm')"
                           onmouseenter="this.style.background = '#5dd5c8'"
                           onmouseleave="this.style.background = '#00d7c6'"
                           style="width: 120%; margin-left: -20%; background: #00d7c6 ;border: none">登录
                </el-button>
              </el-form-item>
              <el-form-item>
                <el-button type="text" style="color: #00d7c6">微信登录</el-button>
                <el-button type="text" style="color: #00d7c6">扫码登录</el-button>
                <el-button type="text" style="color: #00d7c6" v-on:click="replace()">免费注册</el-button>
              </el-form-item>


            </el-form>

            <el-form ref="registForm" :model="form" :rules="rules" v-if="!loginRegistReplace" label-width="80px"
                     class="main-box">


              <el-form-item label="账号" prop="username">
                <el-input type="text" placeholder="请输入账号" v-model="form.username"/>
              </el-form-item>

              <el-form-item prop="isverify" style="width: 115%;margin-left: -15%">
                <!--          <el-input type="text" placeholder="请输入验证码" v-model="form.securityCode" style="width: 70%"></el-input>-->
                <el-popover
                  placement="right"
                  width="400"
                  heigth="600"
                  trigger="click">
                  <q-slide-verify v-if="!form.isverify" v-model="form.isverify"></q-slide-verify>
                  <el-button type="success" slot="reference" style="width: 100%" :disabled="form.isverify">
                    {{ form.isverify==false?'点击按钮进行验证':'验证成功' }}
                  </el-button>
                </el-popover>


              </el-form-item>

              <el-form-item>
                <el-button type="primary" v-on:click="onSubmit('registForm')"
                           style="width: 120%; margin-left: -20%; background: #00d7c6 ;border: none">注册
                </el-button>
              </el-form-item>
              <el-form-item>
                <el-checkbox v-model="registChecked" style="font-size: 5px;margin-left: -45px;">同意直聘《用户协议》 《个人信息保护政策》
                </el-checkbox>
                <span>已有账号<el-button type="text" style="color: #00d7c6" v-on:click="replace()">直接登录</el-button></span>
              </el-form-item>

            </el-form>

          </div>

          <br clear="both">

        </div>
      </el-col>
    </el-row>


  </div>
</template>

<script>

  import QSlideVerify from "../../components/qSlideVerify";
  import {mapMutations} from 'vuex';

  export default {
    name: "",
    components: {'q-slide-verify': QSlideVerify},

    data() {
      var checkIsverify = (rule, value, callback) => {
        if (value != true) {
          callback(new Error('请通过安全认证！'));
        } else {
          callback();
        }
      };
      return {
        form: {
          bossAccount: '',
          password: '',
          isverify: false
        },

        // 表单验证，需要在 el-form-item 元素中增加 prop 属性
        rules: {
          bossAccount: [
            {required: true, message: '账号不可为空', trigger: 'blur'}
          ],
          password: [
            {required: true, message: '密码不可为空', trigger: 'blur'}
          ],
          /*isverify: [
            {validator: checkIsverify, trigger: 'blur'}
          ]*/
        },

        // 对话框显示和隐藏
        dialogVisible: false,
        // 登录注册页面切换
        loginRegistReplace: true,
        // 是否勾选注册协议
        registChecked: false,
        //

        imgSrc: 'https://qiubucket.oss-cn-guangzhou.aliyuncs.com/recruitment_web_static/img/bossLoginPageBackgrond.png'

      }
    },
    methods: {
      //登录方法
      onSubmit(formName) {
        // 为表单绑定验证功能

        let _this = this;

        this.$refs[formName].validate((valid) => {
          if (valid) {
            _this.$axios({
              method: 'post',
              url: _this.APPConstant.API_HEAD + '/re-company-boss/login',
              data: _this.form,
              headers: {
                "Accept": "application/json",
              }
            }).then(res => {
              // 将用户信息和token保存到localStorage中
              localStorage.setItem(_this.APPConstant.ACCESS_BOSS_TOKEN, res.data.data.boss_token)
              localStorage.setItem("bossDetail", JSON.stringify(res.data.data.reCompanyBoss))
              _this.$router.push('/boss/bossMainPage')
            }).catch(error => {
              alert('账号或密码错误');
            });
          } else {
            this.dialogVisible = true;
            this.$message({
              message: '请输入完整信息',
              type: 'error'
            });
            return false;
          }
        });
      },
      //登录注册页面切换方法
      replace() {
        this.loginRegistReplace = !this.loginRegistReplace;
        this.form.isverify = false;
      }
    },
    mounted() {

    }
  }
</script>

<style scoped>

  #app-login {
    overflow: hidden;
  }

  .background {
    width: 100%;
    height: 100%; /**宽高100%是为了图片铺满屏幕 */
    z-index: -1;
    position: absolute;
    top: 0px;
  }

  .front {
    z-index: 1;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    /*width: 100px;*/
    /*height: 100px;*/
    /*background: red;*/
  }

  .login-box {
    border: 1px solid #DCDFE6;
    /*width: 800px;
    height: 800px;*/
    margin: 130px auto;
    /*padding: 35px 35px 15px 35px;*/
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    box-shadow: 0 0 25px #909399;
    background: white;
  }

  .main-box {
    margin-top: 10px;
  }

  .form-aside {
    float: left;
    width: 30%;
    height: 400px;
    background: #f8fcff;
  }

  .logo {
    float: none;
    display: block;
    width: 200px;
    height: 40px;
    margin: 88px 0 0 30px;
    padding: 0;
  }

  .logo-image {
    width: 42px;
    height: 42px;
    float: left;
  }

  .logo-text {
    margin-left: 45px;
  }

  .logo-text > p {
    color: #00c2b3;
    line-height: 20px;
    font-size: 14px;
    margin: 0;
  }

  .aside-other {
    float: none;
    display: block;
    width: 200px;
    height: 40px;
    margin: 50px 0 0 28px;
    padding: 0;
  }

  .other-text {
    margin-left: 44px;
  }

  .other-text > p {
    color: #8d92a1;
    line-height: 20px;
    font-size: 13px;
    margin: 0;
  }

  .form-main {
    float: left;
    /*position: relative;
    top: 18%;
    left: 12%;
    width: 45%;*/
    width: 320px;
    padding: 60px 60px 20px 60px;
  }

  .login-title {
    text-align: center;
    margin: 0 auto 40px auto;
    color: #303133;
  }

  /*  ::-webkit-scrollbar {
      width: 0 !important;
    }

    ::-webkit-scrollbar {
      width: 0 !important;
      height: 0;
    }*/
</style>



